/* glob  */

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

:root {
  --white: #ffffff;
  --black: #171717;
  --background: #fafafa;
  --background-accent: #f3f9ff;
  --background-small: #d3e1ff;
  --accent: #558cfc;
  --accent-light: #91b5ff;
  --text-gray: #5d5d5d;
  --text-light: #a2a2a2;
  --border: #e5e7eb;
}

body {
  position: relative;
  font-family: "PTSans", sans-serif;
  color: var(--black);
  font-size: 16px;
  line-height: 140%;
}

.container {
  max-width: 1640px;
  margin: 0 auto;
}

.title,
.title--secondary,
.title--third {
  font-family: "PTSerif", serif;
  font-weight: 700;
  line-height: 120%;
}

.title {
  font-size: 46px;
}

.title u {
  color: var(--accent);
  text-decoration: none;
}

.title--secondary {
  font-size: 46px;
  text-align: center;
}

.title--third {
  font-size: 36px;
  text-align: center;
}

.title--service {
  font-size: 64px;

  & u {
    display: block;
    font-size: 36px;
    line-height: 120%;
  }
}

.no-scroll {
  overflow-y: hidden;
}

.block-accent {
  color: var(--white);
  background-color: var(--accent);
}

.input {
  padding: 12px 12px 12px 0;
  width: 100%;
  height: 46px;
  font-size: 14px;
  border-bottom: 1px solid var(--text-light);
  background-color: transparent;
}

.input::placeholder {
  font-size: 16px;
  line-height: 140%;
  color: #2b547480;
}

input[type="checkbox"] {
  position: relative;
  appearance: none;
  margin-top: 6px;
  width: 16px;
  height: 16px;
  left: 0;
  top: 2px;
  border: 1px solid #0000001a;
  border-radius: 4px;
  box-shadow: 0px 1px 2px 0px #0000000d;
}

input[type="checkbox"]:checked::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  left: 2px;
  top: 1px;
  background-image: url("./../img/icons/checkbox.svg");
}

.input--textarea {
  height: 84px;
  font: inherit;
  resize: none;
  overflow: hidden;

  &::placeholder {
    transform: translateY(36px);
  }
}

.input--error {
  color: red;
  border-color: red;
}

.button {
  padding: 14px 20px;
  min-height: 50px;
  background-color: var(--accent);
  color: var(--white);
  font-size: 16px;
  text-align: center;
}

.button--transparent {
  background-color: transparent;
  color: var(--accent);
  border: 1px solid var(--accent)
}

.button--inverse {
  color: var(--accent);
  background-color: var(--white);
}

.button--small {
  padding: 0;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button--more {
  min-width: 149px;
  font-weight: 700;
  background-color: var(--background-small);
  color: var(--accent);
}

.button--more:hover {
  background-color: var(--accent);
  color: var(--white);
  transition: background-color 0.7s;
}

.button:hover {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(0deg, #558cfc, #558cfc);
  transition: background-color 0.7s;
}

.button--transparent:hover {
  background: var(--background-small);
  transition: background-color 0.7s;
}

.button--small:hover {
  background-color: var(--accent);
  transition: background-color 0.7s;
}

.button--small:hover .header__icon {
  color: var(--white);
}

.subtitle {
  margin-top: 10px;
  margin-bottom: 60px;
  font-size: 18px;
  text-align: center;
}

.list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.list li {
  padding-left: 17px;
  position: relative;
}

.list li::before {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: var(--accent);
  border-radius: 50%;
}

.list-check {
  display: flex;
  flex-direction: column;
  gap: 11px;
  color: var(--text-gray);
}

.list-check__item {
  position: relative;
  padding-left: 30px;
}

.list-check__item::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 23px;
  left: 0;
  top: 0;
  background: url("./../img/icons/check.svg") no-repeat;
}

.list-services {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.list-services__item {
  position: relative;
  padding: 24px 61px 24px 32px;
  border-top: 1px solid var(--border);
  border-left: 1px solid var(--border);
  border-right: 1px solid var(--border);
}

.list-services__item:last-child {
  border-bottom: 1px solid var(--border);
}

.list-services__item a {
  font-size: 18px;
  font-weight: 700;
}

.list-services__item a:hover {
  color: var(--accent);
}

.list-services__item::before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
  background: url("./../img/icons/right.svg") no-repeat;
  background-size: 26px 26px;
}

.important {
  margin-bottom: 32px;
  padding: 25px;
  text-align: center;
  background-color: #fefce8;
  border: 1px solid #fff085;

  & span {
    color: #d08700;
  }
}

blockquote {
  position: relative;
  margin-top: 30px;
  margin-bottom: 20px;
  padding: 26px 33px 26px 32px;
  background-color: #e9f4ff;
  border-left: 4px solid var(--accent);

  & p {
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 136%;
    font-style: italic;
  }

  & span {
    font-weight: 700;
    color: var(--text-gray);
  }
}

blockquote::before {
  content: "";
  position: absolute;
  width: 36px;
  height: 36px;
  left: -19px;
  top: -19px;
  border-radius: 50%;
  background: var(--accent) url("./../img/icons/quote.svg") center no-repeat;
}

.warning {
  margin: 27px 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 26px 26px 26px 82px;
  background-color: #fefce8;
  border: 1px solid #f5c0444d;

  & b {
    position: relative;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;

    &::before {
      content: "";
      position: absolute;
      width: 36px;
      height: 36px;
      left: -56px;
      top: 0;
      border-radius: 50%;
      background: #f5c044 url("./../img/icons/warning.svg") center no-repeat;
    }
  }
}

.slider-navigation {
  position: absolute;
  top: 50%;
  left: -24px;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  width: calc(100% + 48px);
  z-index: 2;
}

.swiper-button-prev,
.swiper-button-next {
  position: relative;
  margin: 0;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: var(--white);
  border: 1px solid var(--border);
  box-shadow: 0px 4px 6px -4px #0000001a, 0px 10px 15px -3px #0000001a;
  cursor: pointer;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: var(--background-small);
}

.swiper-button-next::after,
.swiper-button-prev::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
}

.swiper-button-next::after {
  background: url("./../img/icons/right.svg") center no-repeat;
}

.swiper-button-prev::after {
  background: url("./../img/icons/left.svg") center no-repeat;
}

::-webkit-scrollbar-track {
  background-color: #d9e4fa;
}

::-webkit-scrollbar-thumb {
  border-radius: 60px;
  background-color: var(--accent);
}

::-webkit-scrollbar {
  border-radius: 60px;
  height: 6px;
  width: 6px;
}

/* animation photo */
.img-animation img {
  -webkit-transition: scale 5s ease-in-out;
  transition: scale 5s ease-in-out;
}

.img-animation {
  overflow: hidden;
  position: relative;
  border-radius: 5px;
}

.img-animation::before {
  display: block;
  pointer-events: none;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  right: 0;
  bottom: 0;
  background-color: #4C74C2;
  opacity: 0.5;
  z-index: 2;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}

.img-animation:hover img {
  scale: 1.3;
}

.img-animation:hover::before {
  width: 100%;
}

@media (max-width: 1640px) {
  .container {
    padding: 0 20px;
  }
}

@media (max-width: 1440px) {
  .title {
    font-size: 40px;
  }
}

@media (max-width: 1154px) {
  .title {
    font-size: 35px;
  }
}

@media (max-width: 1023px) {
  .title {
    font-size: 28px;
    line-height: 111%;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 12px;
  }

  .button {
    padding: 12px 19px;
  }

  .title--secondary,
  .title--third {
    line-height: 110%;
  }

  .title--secondary {
    font-size: 28px;
  }

  .title--third {
    font-size: 24px;
  }

  .title--service {
    font-size: 28px;
    line-height: 110%;

    & u {
      margin-top: 10px;
      font-size: 20px;
      line-height: 110%;
    }
  }

  .subtitle {
    margin-bottom: 39px;
    font-size: 16px;
  }

  .input--textarea {
    height: 46px;

    &::placeholder {
      transform: translateY(0px);
    }
  }

  blockquote {
    padding: 20px;

    & p {
      font-size: 16px;
      line-height: 136%;
    }
  }

  .important {
    padding: 20px;
    text-align: left;
  }

  .warning {
    margin: 36px 0 13px 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 20px;
    background-color: #fefce8;
    border: 1px solid #f5c0444d;

    & b {
      position: relative;
      padding-left: 40px;
      font-family: "PTSerif", serif;
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;

      &::before {
        width: 26px;
        height: 26px;
        left: 0;
        background-size: 15px;
      }
    }
  }

  .list-check {
    gap: 8px;
  }

  .list-check__item {
    position: relative;
    padding-left: 28px;
  }

  .list-check__item::before {
    width: 17px;
    height: 17px;
    background-size: 17px 17px;
  }

  .list-services__item {
    padding: 18px 44px 18px 16px;
  }

  .list-services__item a {
    font-size: 16px;
  }

  .swiper-horizontal>.swiper-scrollbar,
  .swiper-scrollbar.swiper-scrollbar-horizontal {
    height: 6px;
    background-color: #d9e4fa;
  }

  .swiper-scrollbar-drag {
    background-color: var(--accent);
  }

  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
}

@media (min-width: 768px) {
  .swiper-scrollbar {
    display: none;
  }
}

.main {
  overflow: hidden;
}