.text-navigation {
  padding: 41px 0;

  &__title {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 110%;
  }
}

.link-list {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);

  & a {
    font-weight: 700;
    color: var(--accent);
  }

  &__item {
    position: relative;
    display: block;
    width: 100%;
    border-bottom: 1px solid var(--border);
    transition: 0.3s;
    overflow: hidden;

    &[open] {
      padding-bottom: 83px;
    }
  }

  &__item[open] > .link-list__title::before {
    transform: rotate(180deg);
  }

  &__title {
    position: relative;
    padding: 21px 42px 25px 24px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    transition: .3s;

    &::marker {
      content: '';
    }
    
    &::-webkit-details-marker {
      display: none;
    }
    
    &::before {
      content: "";
      position: absolute;
      right: 30px;
      top: 21px;
      display: block;
      width: 26px;
      height: 26px;
      background-image: url("./../img/icons/down.svg");
      transform-origin: center;
      transition: .3s;
    }
  }

  &__inner {
    margin-bottom: -69px;
    padding: 0 24px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__link {
    position: relative;
    padding-left: 30px;
  }

  &__link::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 3px;
    left: 0;
    top: 10px;
    background-color: var(--accent);
  }
}

.link-list__item--main > .link-list__title {
  padding: 30px 64px 30px 24px;
}

.link-list__item--main[open] {
  padding-bottom: 0;
}

@media (max-width: 768px) {
  .text-navigation__title {
    font-size: 18px;
  }

  .link-list {
    &[open] {
      padding-bottom: 83px;
    }

    &__title {
      padding: 16px 42px 16px 16px;
      font-size: 16px;

      &::before {
        right: 16px;
        top: 14px;
      }
    }

    &__inner {
      padding: 0 16px;
      gap: 7px;
    }
  }

  .link-list__item--main > .link-list__title {
    padding: 21px 42px 21px 16px;
  }
}