.features {
  padding: 80px 0 120px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }
}

.feature {
  padding: 24px 24px 24px 28px;
  width: 32%;
  min-height: 134px;
  display: flex;
  flex-direction: column;
  border-left: 4px solid var(--accent);
  box-shadow: 0px 10px 15px -3px #0000001a;

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  & span {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
  }

  & p {
    margin-top: auto;
    width: 100%;
    color: var(--text-gray);
  }
}

@media (max-width: 1100px) {
  .feature {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .features {
    padding: 40px 0;

    &__list {
      flex-direction: column;
      gap: 20px;
    }
  }

  .feature {
    padding: 26px 20px 14px 15px;
    width: 100%;
    min-height: 100%;

    &__wrapper {
      width: 100%;
    }

    & span {
      font-size: 28px;
      line-height: 110%;
    }

    & p {
      margin-top: 19px;
      font-size: 16px;
    }
  }
}