.sitemap {
  padding: 41px 0 121px 0;

  &__wrapper {
    display: flex;
    gap: 5%;
  }

  &__flex {
    width: 71%;
  }

  & div.link-list__title {
    border-bottom: 1px solid var(--border);
  }

  & div.link-list__title::before {
    display: none;
  }
}

@media (max-width: 768px) {
  .sitemap {
    &__wrapper {
      display: block;
    }

    &__flex {
      width: 100%;
    }
  }
}