// faq

.faq {
  padding: 84px 0 120px 0;

  &__title {
    margin-bottom: 14px;
  }

  &__tabs {
    margin-bottom: 10px;
    display: flex;
    gap: 8px;
  }

  & .tab {
    width: 17%;
    min-height: 54px;
    padding: 16px;
    text-align: center;
    background-color: #f9f9f9;

    &:hover {
      background-color: var(--background-small);
    }
  }

  & .tab--active {
    color: var(--white);
    background-color: var(--accent);
    transition: background-color 0.5s;
  }

  &__list {
    display: none;
  }

  &__list.active {
    display: flex;
    flex-direction: column;
    border: 1px solid var(--border);
  }
}

.question {
  position: relative;
  display: block;
  width: 100%;
  border-bottom: 1px solid var(--border);
  transition: 0.3s;
  overflow: hidden;
}

.question[open] {
  padding-bottom: 100px;
  background-color: #f9f9f9;
}

.question__title {
  position: relative;
  padding: 32px 24px 30px 24px;
  padding-right: 42px;
  font-family: "PTSerif", serif;
  font-size: 20px;
  line-height: 110%;
  font-weight: 700;
  cursor: pointer;
  transition: .3s;

  &:hover {
    background-color: var(--background-small);
    border-radius: 8px;
    transition: border-radius 0.5s;
  }

  &::marker {
    content: '';
  }

  &::-webkit-details-marker {
    display: none;
  }

  &::after {
    content: "";
    position: absolute;
    right: 30px;
    top: 30px;
    display: block;
    width: 26px;
    height: 26px;
    background: url("./../img/icons/down.svg");
    transform-origin: center;
    transition: .3s;
  }
}

.question[open] .question__title::after {
  transform: rotate(180deg);
}

.question__content {
  padding: 2px 24px 0 24px;
  font-size: 16px;
  margin-bottom: -69px;
}

.question__content p:last-child {
  margin-bottom: 0;
}

.question__content p:first-child {
  margin-top: 0;
}

.service-faq .question__title {
  display: flex;
  gap: 14px;
}

.service-faq {
  padding: 80px 0 120px 0;
  background-color: var(--background);

  &__title {
    margin-bottom: 48px;
  }

  & .faq__list.active {
    gap: 10px;
    border: none;
  }

  & .question {
    border: 1px solid var(--border);
  }

  & .question[open] {
    background-color: transparent;
    padding-bottom: 79px;
  }

  & .question__title {
    padding: 20px;
    font-size: 18px;
    line-height: 120%;

    &::after {
      right: 20px;
      top: 20px;
    }
  }

  & .question__content {
    color: var(--text-gray);
  }
}

@media (max-width: 768px) {
  .faq {
    padding: 40px 0;

    &__title {
      margin-bottom: 10px;
    }

    & .subtitle {
      margin-bottom: 30px;
    }

    &__tabs {
      flex-wrap: wrap;
    }

    & .tab {
      width: auto;
      min-height: 40px;
      padding: 10px;
      font-size: 14px;

      &:hover {
        background-color: var(--accent);
      }
    }
  }

  .question {
    position: relative;
    display: block;
    width: 100%;
    border-bottom: 1px solid var(--border);
    transition: 0.3s;
    overflow: hidden;
  }

  .question[open] {
    padding-bottom: 81px;
    background-color: #f9f9f9;
  }

  .question__title {
    padding: 16px 14px;
    padding-right: 65px;
    font-size: 16px;
    line-height: 110%;

    &:hover {
      background-color: #f9f9f9;
    }

    &::after {
      right: 14px;
      width: 20px;
      height: 20px;
      background-size: 20px 20px;
    }
  }

  .question__content {
    padding: 0 14px 0 14px;
    margin-bottom: -69px;
  }

  .service-faq {
    padding: 40px 0;

    &__title {
      margin-bottom: 40px;
    }

    & .question[open] {
      padding-bottom: 83px;
    }

    & .question__title {
      padding: 14px;
      padding-right: 35px;
      font-size: 16px;
      line-height: 110%;

      &::after {
        right: 14px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    & .question__icon {
      width: 18px;
      height: 18px;
    }

    & .question__content {
      color: var(--text-gray);
    }
  }
}


// services-page

.faq-page {
  padding: 80px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 416px 1fr;
    gap: 41px;
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__icon {
    stroke: var(--accent);
  }

  &__title {
    text-align: left;
  }

  & .faq__tab {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  &__button {
    margin-top: 40px;
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .faq-page {
    padding: 40px 0;

    &__wrapper {
      display: flex;
      flex-direction: column;
    }
  }
}