/* intro */
.intro {
  position: relative;
  background: var(--background-accent) url("./../img/sections/intro/background.webp");
}

.intro__content {
  height: 858px;
  display: flex;
  flex-wrap: wrap;
}

.intro--service .intro__content {
  height: 837px;
}

.intro__wrapper {
  position: relative;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  margin-right: 5%;
  max-width: 978px;
}

.intro--service .intro__wrapper {
  margin-top: 30px;
}

.intro__title {
  margin-bottom: 20px;
}

.intro--service .intro__title {
  margin-bottom: 30px;
}

.intro__subtitle {
  margin-bottom: 30px;
  font-size: 20px;
  line-height: 140%;
  text-align: left;
}

.intro__list {
  margin-bottom: 60px;
  flex-direction: row;
  gap: 13px 0;
  width: 61%;
}

.intro--service .intro__list {
  margin-bottom: 40px;
}

.intro__list li {
  width: 50%;
  font-size: 18px;
}

.intro__buttons {
  display: flex;
  gap: 12px;
}

.intro--service .intro__button {
  width: 243px;
}

.intro__doctor {
  position: relative;
  align-self: flex-end;
}

.intro__bage {
  bottom: 143px;
  left: 50%;
  transform: translateX(-50%);
  width: 427px;
}

.bage {
  position: absolute;
  padding: 12px 18px 12px 72px;
  background-color: var(--white);
}

.bage::before {
  content: "";
  position: absolute;
  width: 46px;
  height: 46px;
  left: 16px;
  top: 12px;
  background: url("./../img/icons/intro-doctor.svg") no-repeat;
}

.bage__name {
  margin-bottom: 4px;
  font-family: "PTSerif", serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 120%;
  color: var(--accent);
}

.intro__features {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 42px 0 31px 0;
  padding-left: calc(50vw - 854px);
  width: 62%;
}

.intro-features__list {
  display: flex;
}

.intro-features__item {
  padding: 0 33px;
  display: flex;
  flex-direction: column;
  gap: 14px;
  border-right: 1px solid var(--border);
}

.intro-features__item--mobile {
  display: none;
}

.intro-features__item:last-child {
  border-right: none;
}

.intro-features__item b {
  font-family: "PTSerif", serif;
  font-weight: 700;
  font-size: 20px;
}

.intro-features__item span {
  font-size: 36px;
}

.intro__plashka {
  position: absolute;
  top: 51%;
  left: 60%;
  padding: 8px 24px 8px 32px;
  width: max-content;
  background-color: var(--background-small);
  z-index: 2;
}

.intro__plashka p {
  display: flex;
  flex-direction: column;
}

p.intro__plashka-mobile {
  display: none;
}

.intro__plashka--right {
  top: 32%;
  left: calc(50vw + 658px);
}

.intro__plashka::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 24px;
  left: 8px;
  top: 8px;
  background-image: url("./../img/icons/point.svg");
}

.intro__price {
  position: relative;
  margin-bottom: 10px;
  padding: 20px;
  max-width: 497px;
  color: var(--text-gray);
  border: 1px solid var(--accent);
}

.intro__price-title {
  margin-bottom: 6px;
  font-weight: 700;
}

.intro__price-sale {
  padding: 3px 9px;
  top: 20px;
  right: 20px;
}

.intro__price-number {
  font-family: "PTSerif", serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 120%;
  color: var(--accent);

  & s {
    margin-right: 12px;
    font-size: 20px;
    color: #6a7282;
  }
}

.intro-small {
  padding: 30px 0 60px 0;
  background: #f7f7f7 url("./../img/sections/intro/background-small.webp") no-repeat;
  background-position: center;

  &__title {
    margin-bottom: 4px;
    text-align: left;
  }

  & .subtitle {
    margin-top: 16px;
    margin-bottom: 0;
    width: 83%;
    font-size: 20px;
    line-height: 135%;
    text-align: left;
  }

  & .breadcrumbs {
    margin-bottom: 40px;
  }
}

/* breadcrumbs */
.breadcrumbs {
  margin-bottom: 48px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 10px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  opacity: 0.5;
  width: 100%;
}

.breadcrumbs li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 10px;
  font-size: 14px;
}

.breadcrumbs li a:hover {
  color: var(--accent);
}

.breadcrumbs li::after {
  content: "—";
  display: block;
  font-size: inherit;
}

.breadcrumbs li:last-of-type::after {
  display: none;
}

@media (max-width: 1820px) {
  .intro {
    height: auto;
  }

  .intro__wrapper {
    max-width: 55%;
  }

  .intro__list {
    width: 70%;
  }

  .intro__plashka--right {
    left: calc(50vw + 600px);
  }
}

@media (max-width: 1639px) {
  .intro__plashka--right {
    left: calc(50vw + 505px);
  }
}

@media (max-width: 1593px) {
  .intro__content {
    height: auto;
  }

  .intro__features {
    position: static;
    padding: 40px;
    width: 100%;
  }
}

@media (max-width: 1440px) {
  .intro__list {
    width: 100%;
  }

  .intro__doctor {
    padding-top: 30px;
    width: 30%;
    height: auto;
  }

  .intro__plashka {
    top: auto;
    bottom: 300px;
  }

  .intro__plashka--right {
    top: auto;
    bottom: 300px;
    left: auto;
    right: 0;
  }

  .intro__bage {
    bottom: 0;
    left: 66%;
  }

  .intro__picture {
    width: 500px;
    height: auto;
  }

  .intro-features__item {
    padding: 0 23px;
  }
}

@media (max-width: 1023px) {
  .intro__subtitle {
    font-size: 16px;
    line-height: 136%;
  }

  .intro__list li {
    font-size: 16px;
  }

  .intro__bage-name {
    font-size: 16px;
  }

  .intro__plashka {
    padding: 6px 8px 8px 26px;
    right: 20%;
    font-size: 12px;
    line-height: 149%;
  }

  .intro__plashka--right {
    right: 0;
  }

  .intro__plashka b span {
    color: var(--accent);
  }

  p.intro__plashka-desktop {
    display: none;
  }

  p.intro__plashka-mobile {
    display: flex;
  }

  .intro__plashka::before {
    left: 6px;
    top: 4px;
  }

  .intro__features {
    padding: 20px;
  }

  .intro-features__item {
    padding: 0 20px;
  }

  .intro-features__item {
    font-size: 14px;
  }

  .intro-features__item span {
    font-size: 28px;
  }
}

@media (max-width: 768px) {

  /* breadcrumbs */
  .breadcrumbs {
    margin-bottom: 27px;
    gap: 0 4px;
  }

  .breadcrumbs li {
    gap: 4px;
    font-size: 12px;
  }

  .breadcrumbs li:last-of-type::after {
    display: none;
  }

  .intro__doctor {
    display: none;
  }

  .intro__wrapper {
    margin-top: 0;
    margin-right: 0;
    max-width: 100%;
  }

  .intro--service .intro__wrapper {
    margin-top: 8px;
  }

  .intro__content {
    padding-top: 10px;
    justify-content: space-between;
  }

  .intro--service .intro__content {
    height: auto;
  }

  .intro__list {
    margin-bottom: 40px;
    flex-direction: column;
    gap: 6px;
    width: 100%;
  }

  .intro__list li {
    padding-left: 14px;
    position: relative;
    width: 100%;
  }

  .intro__buttons {
    margin-bottom: 40px;
    flex-direction: column;
  }

  .intro__features {
    position: static;
    padding: 0 12px;
    width: 100%;
  }

  .intro-features__list {
    flex-direction: column;
  }

  .intro-features__item {
    padding: 20px 0 19px 0;
    display: grid;
    grid-template-columns: 35% 1fr;
    gap: 0;
    align-items: center;
    border-right: none;
    width: 100%;
    border-bottom: 1px solid var(--white);
  }

  .intro-features__item p {
    line-height: 143%;
  }

  .intro-features__item--desktop {
    display: none;
  }

  .intro-features__item--mobile {
    display: grid;
  }

  .intro-features__item:last-child {
    border-bottom: none;
  }

  .intro__plashka {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 30px;
    width: 45%;
  }

  .intro--service .intro__plashka {
    margin-bottom: 0;
  }

  .intro__plashka--right {
    top: 0;
    width: 54%;
  }

  .intro__plashka b span {
    color: var(--accent);
  }

  .intro__plashka::before {
    left: 6px;
    top: 4px;
  }

  .intro--service .intro__title {
    margin-bottom: 21px;
  }

  .intro__subtitle {
    margin-bottom: 19px;

    & b {
      display: block;
    }
  }

  .intro__list li {
    padding-left: 17px;
  }

  .intro__list li::before {
    left: 3px;
  }

  .intro__buttons {
    margin-bottom: 39px;
  }

  .intro__button {
    height: 54px;
  }

  .intro--service .intro__buttons {
    flex-direction: row;
    justify-content: space-between;
  }

  .intro--service .intro__button {
    width: 48%;
  }

  .intro__price {
    margin-bottom: 6px;
    padding: 14px;
    max-width: 100%;
    width: 100%;
  }

  .intro__price-title {
    margin-bottom: 17px;
    font-size: 14px;
    font-weight: 700;
  }

  .intro__price-sale {
    padding: 3px 9px;
    top: 14px;
    right: 14px;
    font-size: 14px;
  }

  .intro__price-note {
    margin-top: -5px;
    font-size: 12px;
  }

  .intro-small {
    padding: 7px 0 38px 0;
    background-size: auto 100%;
    background-position: top right;

    &__title {
      margin-bottom: 4px;
    }

    & .subtitle {
      margin-top: 10px;
      margin-bottom: 0;
      width: 100%;
      font-size: 16px;
      text-align: left;
    }

    & .breadcrumbs {
      margin-bottom: 30px;
    }
  }
}