/* header */

.logo {
  margin-right: 6%;
  display: flex;
  align-items: center;
  gap: 12px;
  width: 248px;
  height: 65px;
}

.logo__text {
  font-family: 'PTSerif', serif;
  font-weight: 700;
  font-size: 21px;
  line-height: 120%;
}

.header__wrapper {
  padding: 26px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}

.header__contacts {
  display: flex;
  justify-content: space-between;
  width: 48%;
}

.header__flex {
  display: flex;
  flex-direction: column;
  gap: 5px;
  min-width: 197px;
  font-size: 14px;
  color: var(--text-light);
}

.header__phone,
.header__city-button,
.header__street,
.header__email {
  position: relative;
  display: flex;
  font-weight: 700;
  color: var(--black);
  line-height: 140%;
}

.header__phone:hover,
.header__city-button:hover,
.header__email:hover {
  color: var(--accent);
}

.header__phone {
  padding-left: 21px;
}

.header__phone::before {
  content: url("../img/icons/menu-phone.svg");
  position: absolute;
  width: 18px;
  height: 20px;
  left: 0;
  top: 4px;
}

.header__city-button,
.header__street,
.header__email {
  padding-left: 24px;
}

.header__city-button {
  min-width: 248px;
  background-color: transparent;
  text-decoration: underline;
}

.header__city-button::before {
  content: url("../img/icons/city.svg");
  position: absolute;
  width: 18px;
  height: 20px;
  left: 0;
  right: 0;
}

.header__email::before {
  content: url("../img/icons/email.svg");
  position: absolute;
  width: 18px;
  height: 20px;
  left: 0;
  right: 0;
}

.header__icon {
  color: var(--accent);
}

.header__buttons {
  margin-left: auto;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  gap: 9px;
  max-width: 420px;
}

.header__mobile {
  display: none;
}

.main-navigation {
  width: 100%;
  background-color: var(--accent);
}

.main-navigation__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1640px;
  margin: 0 auto;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.main-navigation__link {
  position: relative;
  display: flex;
  padding: 20px 0;
  color: var(--white);
}

.main-navigation__link:hover {
  opacity: 0.6;
}

.submenu {
  padding-right: 20px;
  position: relative;
}

.submenu__list {
  display: flex;
  position: absolute;
  top: 62px;
  left: 0;
  flex-direction: column;
  background-color: var(--background);
  width: max-content;
  border: 1px solid var(--border);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

@media (min-width: 769px) {
  .submenu:hover .submenu__list {
    display: flex;
    opacity: 1;
    visibility: visible;
  }

  .submenu:hover.submenu:after {
    transform: rotate(-180deg);
    transition: transform 0.5s;
    opacity: 0.6;
    top: 30px;
  }
}

.submenu__link {
  display: block;
  padding: 20px;
  font-weight: 700;
  color: var(--black);
}

.submenu:after {
  content: "";
  background-image: url("../img/icons/menu-down.svg");
  position: absolute;
  width: 10px;
  height: 10px;
  right: 0;
  top: 25px;
}

.submenu__item {
  border-bottom: 1px solid var(--border);
}

.submenu__link:hover {
  color: var(--accent);
}

@media (max-width: 1640px) {
  .main-navigation__wrapper {
    padding: 0 20px;
  }
}

@media (max-width: 1566px) {
  .logo {
    margin-right: 20px;
  }

  .header__contacts {
    width: 50%;
  }
}

@media (max-width: 1484px) {
  .logo {
    width: auto;
  }
  .logo__text {
    display: none;
  }
}

@media (max-width: 1100px) {
  .main-navigation__list {
    justify-content: flex-start;
    gap: 0 20px;
    flex-wrap: wrap;
  }

  .header__buttons {
    max-width: 300px;
  }
}

@media (max-width: 1033px) {
  .logo {
    margin-right: 0;
  }

  .header__contacts {
    width: 40%;
  }
}

@media (max-width: 1023px) {
  .main {
    padding-top: 72px;
  }

  .header__mobile-button {
    font-size: 14px;
  }

  .header {
    position: relative;
  }

  .header__wrapper {
    padding: 14px 0;
    gap: 0;
  }

  .header__desktop {
    display: none;
  }

  .header__mobile {
    position: fixed;
    top: 0;
    z-index: 10;
    display: flex;
    background-color: var(--white);
  }

  .logo__img {
    width: 34px;
    height: 34px;
  }

  .logo__text {
    width: 136px;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: 0%;
  }

  .header__mobile-icon {
    background-color: transparent;
    width: 22px;
    align-self: center;
  }

  .header__burger {
    position: relative;
    margin-right: 16px;
    display: flex;
    width: 19px;
    height: 44px;
    background-color: transparent;
  }

  .header__burger::before {
    content: "";
    position: absolute;
    width: 19px;
    height: 2px;
    left: 0;
    top: 40%;
    background-color: var(--black);
    border-radius: 30px;
  }

  .header__burger::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 2px;
    left: 0;
    top: 53%;
    background-color: var(--black);
    border-radius: 30px;
  }

  .header__menu-close {
    position: absolute;
    top: 31px;
    right: 31px;
    opacity: 0.5;
  }

  .header__menu-close::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    left: 0;
    top: 40%;
    background-color: var(--black);
    border-radius: 30px;
    transform: rotate(45deg);
  }

  .header__menu-close::after {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    left: 0;
    top: 40%;
    background-color: var(--black);
    border-radius: 30px;
    transform: rotate(-45deg);
  }

  .header__mobile-button {
    margin-left: auto;
    margin-right: 14px;
    min-height: 44px;
    height: 44px;
  }

  .header__background {
    display: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    opacity: 0;
    z-index: -1;
    transition: opacity 0.5s ease;
  }
  
  .main-navigation__wrapper {
    padding: 0;
  }

  .main-navigation--opened .header__background {
    position: fixed;
    display: flex;
    flex-direction: column;
    opacity: 1;
    background: rgba(0, 0, 0, .7);
    z-index: 10;
    transition: opacity 0.5s ease;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .header__menu {
    margin-bottom: 10px;
    padding: 14px 12px 50px 12px;
    position: relative;
    background-color: var(--white);
    width: 88%;
    overflow: scroll;
  }

  .header__menu .logo {
    padding-bottom: 13px;
    height: auto;
    gap: 9px;
  }

  .header__mobile .header__wrapper {
    display: flex;
    width: calc(100vw - 24px);
  }

  .header__contacts {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  .main-navigation {
    padding: 14px 0 3px 0;
    background-color: var(--white);
    border-top: 1px solid var(--border);
    border-bottom: 1px solid var(--border);
  }

  .main-navigation__list {
    flex-direction: column;
  }

  .main-navigation__link {
    padding: 0 0 11px 0;
    color: var(--black);
  }

  .submenu__list {
    display: flex;
    position: static;
    flex-direction: column;
    background-color: var(--white);
    width: 100%;
    border: none;
    opacity: 1;
    max-height: 0;
    transition: max-height 0.5s;
  }

  .submenu .main-navigation__link {
    padding-bottom: 9px;
  }

  .submenu:after {
    content: url("../img/icons/menu-down-black.svg");
    top: 0;
  }

  .submenu--opened .submenu__list {
    margin: 10px 0;
    visibility: visible;
    max-height: 100vh;
  }

  .submenu__link {
    padding: 10px 0;
    font-size: 14px;
  }

  .submenu__item:last-child {
    border: none;
  }

  .header__buttons {
    padding: 14px 0;
    flex-direction: column;
    width: 100%;
  }

  .header__button {
    width: 100%;
  }

  .header__buttons-small {
    padding-top: 14px;
    display: flex;
    justify-content: space-between;
  }

  .header__buttons-small .header__button {
    width: 48%;
  }

}