// services

.services {
  padding: 120px 0;

  &__content {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__slider {
    padding: 20px 0;
  }

  &__link {
    margin: 0 auto;
  }
}

@media (min-width: 769px) {
  .services .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px 15px;
  }
}

.service {
  position: relative;
  padding: 30px;
  width: 24%;
  box-shadow: 0px 4px 30px 0px #0000000A;

  &__wrapper {
    min-height: 220px;
    display: flex;
    flex-direction: column;
  }

  &__name {
    margin-bottom: 10px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  &__description {
    color: var(--text-gray);
  }

  &__price {
    margin-top: auto;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--accent);
  }

  &__icon {
    position: absolute;
    right: 10px;
    bottom: 10px;
    fill: #236bfe1c;
  }

  &:hover {
    background-color: var(--accent);
    color: var(--white);
    transition: background-color 0.5s, color 0.5s;
  }

  &:hover .service__description {
    color: var(--white);
  }

  &:hover .service__price {
    color: var(--white);
  }

  &:hover .service__icon {
    fill: #ffffff1c;
  }
}

@media (max-width: 1200px) {
  .service {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .services {
    padding: 40px 0;
    background-color: var(--background);

    &__content {
      display: flex;
      flex-direction: column;
      gap: 0;
    }

    &__title {
      margin-bottom: 30px;
    }

    &__link {
      margin: auto;
      margin-top: 18px;
      width: 100%;
    }
    
    &__slider {
      position: relative;
      padding: 0;
      width: 100%;
      min-height: 312px;
      overflow: hidden;
    }
  }

  .service {
    padding: 20px;
    width: 100%;
    min-height: 280px;
    box-shadow: none;
    background-color: var(--white);

    &__wrapper {
      min-height: 240px;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 110%;
    }

    &__price {
      font-size: 18px;
      line-height: 110%;
    }
  }
}