// ways

.ways {
  padding: 120px 0 123px 0;

  &__title {
    margin-bottom: 79px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.way {
  padding: 34px;
  width: 32%;
  height: 494px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 18px;
  color: var(--text-gray);
  border: 1px solid var(--border);
  
  &__wrapper {
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__icon {
    margin-bottom: 26px;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--accent);
  }

  &__title {
    margin-top: 6px;
    margin-bottom: 24px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--accent);
  }

  &__subtitle {
    position: relative;
    padding-left: 17px;
    width: max-content;
  }

  &__subtitle::before {
    content: "";
    position: absolute;
    width: 6px;
    height: 6px;
    left: 3px;
    top: 10px;
    border-radius: 50%;
    background-color: var(--accent);
  }

  &__line {
    margin: 24px 0 26px 0;
    width: 100%;
    height: 1px;
    background-color: var(--border);
  }

  &__button {
    margin-top: auto;
    width: 100%;
    height: 54px;
    font-weight: 700;
  }

  &:hover {
    background-color: var(--background-small);
    transition: background-color 0.5s;
  }
}

.way--accent {
  margin-top: -15px;
  height: 523px;
  background-color: var(--accent);
  color: var(--white);
  box-shadow: 0px 23px 40.2px 0px #00000024;
  border: none;

  & .way__icon {
    background-color: #ffffff33;
  }

  & .way__title {
    margin-top: 11px;
    color: var(--white);
  }

  & .way__subtitle::before {
    background-color: var(--white);
  }

  &:hover {
    background-color: var(--accent-light);
  }
}

.way--accent .way__button:hover {
  color: var(--white);
}

@media (min-width: 769px) and (max-width: 1023px) {
  .way__subtitle {
    width: 100%;
    text-align: left;
  }
}

@media (max-width: 1023px) {
  .way {
    padding: 19px;

    &__wrapper {
      width: 100%;
    }

    &__title {
      font-size: 24px;
      line-height: 107%;
    }
  }
}

@media (max-width: 814px) {
  .way br {
    display: none;
  }
}

@media (max-width: 768px) {
  .ways {
    padding: 40px 0;

    &__title {
      margin-bottom: 27px;
    }

    &__list {
      flex-direction: column;
      gap: 20px;
    }
  }

  .way {
    width: 100%;
    height: auto;
    font-size: 16px;

    &__icon {
      margin-bottom: 20px;
      width: 100px;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: var(--accent);
    }

    &__note {
      font-size: 18px;
      font-weight: 700;
    }

    &__title {
      margin-top: 6px;
      margin-bottom: 18px;
    }

    &__line {
      margin: 20px 0 19px 0;
    }

    &__button {
      margin-top: 20px;
    }
  }

  .way--accent {
    margin-top: 0;
    height: auto;
    box-shadow: none;
  }
}

@media (max-width: 320px) {
  .way__subtitle {
    width: 100%;
    text-align: left;
  }
}