// promos

.promos {
  padding: 80px 0 120px 0;

  &__title {
    margin-bottom: 80px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.sale {
  position: absolute;
  background-color: #fb2c36;
  color: var(--white);
  font-weight: 700;
  text-align: center;
}

.promo {
  position: relative;
  padding: 34px;
  width: 33%;
  min-height: 416px;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);

  &:hover {
    background-color: var(--background-small);
    transition: background-color 0.5s;
  }

  &__sale {
    position: absolute;
    right: 0;
    top: -24px;
    padding: 10px 20px;
    width: 148px;
  }

  &__name {
    margin-bottom: 10px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 120%;
  }

  &__cost {
    margin-bottom: 24px;
    color: var(--accent);
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
  }

  &__button {
    margin-top: auto;
    height: 54px;
    font-weight: 700;
  }

  &--accent {
    background-color: #fe7c45;
    color: var(--white);
    border: none;
    font-size: 18px;

    & .promo__name {
      margin-bottom: 20px;
    }

    & .promo__list {
      margin-top: auto;
      margin-bottom: 10px;
      flex-direction: row;
      gap: 0;
      justify-content: space-between;
      font-family: "PTSerif", serif;
      font-size: 24px;
      text-align: center;
      color: var(--white);
    }

    & .promo__item {
      padding: 26px;
      width: 32%;
      height: 93px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      background-color: #ffffff24;

      & span {
        margin-right: 6px;
        font-size: 46px;
        line-height: 90%;
      }
    }

    & .promo__item::before {
      content: "";
    }
  }
}

.promo--accent .button {
  margin-top: 0;
  background-color: var(--white);
  color: #fe7c45;
}

.promo--accent.promo:hover {
  opacity: 0.8;
  background-color: #fe7c45;
}

.promo--accent .button:hover {
  background: var(--white);
}

@media (max-width: 1300px) {
  .promos__list {
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }

  .promo {
    width: 48%;

    &__name {
      font-size: 29px;
    }
  }
}

@media (max-width: 768px) {
  .promos {
    padding: 40px 0;

    &__title {
      margin-bottom: 40px;
    }

    &__list {
      display: flex;
      flex-direction: column;
      gap: 30px;
    }
  }

  .promo {
    padding: 20px;
    width: 100%;
    min-height: 328px;

    &__sale {
      top: -17px;
      padding: 6px 14px;
      width: 114px;
    }

    &__name {
      font-size: 24px;
      line-height: 110%;
    }

    &__cost {
      font-size: 20px;
      line-height: 110%;
    }

    &__list {
      gap: 10px;
      font-size: 16px;
    }

    &__item {
      padding-left: 25px;
    }

    &__item::before {
      width: 18px;
      height: 18px;
    }

    &--accent {
      min-height: 399px;
      font-size: 16px;

      & .promo__name {
        margin-bottom: 10px;
      }

      & .promo__list {
        flex-wrap: wrap;
        gap: 10px;
        font-size: 18px;
        line-height: 110%;
      }

      & .promo__item {
        padding: 16px;
        width: 48%;
        height: auto;

        & span {
          font-size: 32px;
        }

        &--big {
          width: 100%;
        }
      }
    }
  }

}

// promos-page

.promos-page {
  padding: 80px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 60px 20px;
  }

  & .doctor__link {
    border: none;
  }
}

.promos-page__item {
  width: 32%;
}

.promos-page .promo--accent {
  & .promo__button {
    font-family: "PTSans", sans-serif;
  }
}

@media (max-width: 1440px) {
  .promos-page__item {
    width: 48%;
  }
}

@media (min-width: 769px) {
  .promos-page .promo--accent {
    width: 100%;
    min-height: auto;
    background-image: url("./../img/sections/promos/background-white.webp");

    & .promo__item {
      width: 24%;
    }

    & .promo__button {
      position: relative;
      width: 24%;
      height: 100%;
      text-align: left;
      font-family: "PTSans", sans-serif;

      &::before {
        content: "";
        position: absolute;
        width: 26px;
        height: 26px;
        right: 40px;
        top: 50%;
        transform: translateY(-50%);
        background: url("./../img/icons/right-orange.svg");
      }
    }
  }
}

@media (max-width: 768px) {
  .promos-page {
    padding: 40px 0;

    &__list {
      flex-direction: column;
      gap: 30px;
    }

    .promos-page__item {
      width: 100%;
    }

    & .promo--accent .promo__name,
    & .promo--accent .subtitle {
      text-align: left;
    }

    & .promo--accent .promo__button {
      font-family: "PTSans", sans-serif;
      width: 100%;
    }
  }
}