// patient

.patient {
  padding: 120px 0;
  background: var(--background) url("./../img/sections/patient/background.webp");

  &__title {
    margin-bottom: 60px;
  }

  &__name {
    position: absolute;
    bottom: 18px;
    left: 18px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--white);
  }
}

@media (min-width: 769px) {
  .patient .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }
}

.patient__item {
  position: relative;
  padding: 0;
  width: 32%;
  height: 192px;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
  object-position: center;
  box-shadow: 0px 4px 30px 0px #0000000A;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(0deg, rgba(0, 10, 38, 0.72) 0%, rgba(0, 10, 38, 0) 100%);
    ;
  }

  & img {
    width: 100%;
  }
}

@media (max-width: 1200px) {
  .patient__item {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .patient {
    padding: 40px 0;
    background-size: auto 100%;

    &__title {
      margin-bottom: 40px;
    }

    &__slider {
      position: relative;
      padding-bottom: 48px;
    }

    &__name {
      bottom: 14px;
      left: 14px;
      font-size: 18px;
      line-height: 110%;    }
  }

  .patient__item {
    width: 100%;
    height: 180px;
  }
}