.formats {
  padding: 120px 0 80px 0;
  background-color: var(--background);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.format {
  padding: 30px;
  width: 32%;
  min-height: 242px;
  display: flex;
  flex-direction: column;
  border-top: 4px solid var(--accent);
  background-color: var(--white);
  box-shadow: 0px 4px 6px -4px #0000001a, 0px 10px 15px -3px #0000001a;

  &__icon {
    margin-right: 19px;
    float: left;
  }

  &__name {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 26px;
    line-height: 120%;
  }

  &__description {
    margin-top: auto;
    color: var(--text-gray);
  }
}

@media (max-width: 768px) {
  .formats {
    padding: 40px 0;

    &__title {
      margin-bottom: 37px;
    }

    &__list {
      flex-direction: column;
      gap: 20px;
    }
  }

  .format {
    padding: 20px 20px 20px 14px;
    width: 100%;
    min-height: 100%;

    &__icon {
      margin-right: 17px;
      width: 24px;
      height: 24px;
    }

    &__name {
      font-size: 18px;
      line-height: 110%;
    }

    &__description {
      margin-top: 19px;
      padding-left: 6px;
    }
  }
}