// help

.help {
  padding: 80px 0;
  background-image: url("./../img/sections/help/background.webp");

  &__title {
    margin-bottom: 60px;
  }

  &__wrapper {
    min-height: 122px;
  }

  &__item {
    padding: 40px 30px 30px 30px;
    display: flex;
    flex-direction: column;
    width: 32%;
    height: auto;
    text-align: center;
    background-color: var(--white);
    box-shadow: 0px 4px 35.1px 0px #00000014;
  }

  &__icon {
    margin: 0 auto;
    margin-bottom: 14px;
  }

  &__name {
    margin-bottom: 4px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    color: var(--black);
  }

  &__description {
    color: var(--text-gray);
  }

  &__item:hover {
    background-color: var(--background-small);
    transition: background-color 0.5s;
  }
}

@media (min-width: 769px) {
  .help .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px 0;
    align-items: stretch;
  }
}

@media (max-width: 768px) {
.help {
    padding: 40px 0;
    background-size: auto 100%;

    &__content {
      display: flex;
      flex-direction: column;
      gap: 0;
    }

    &__title {
      margin-bottom: 30px;
    }
    
    &__slider {
      position: relative;
      width: 100%;
      min-height: 205px;
      overflow: hidden;
    }

    &__item {
      padding: 20px;
      width: 100%;
      min-height: 152px;
      background-color: var(--white);
    }

    &__wrapper {
      min-height: 112px;
    }
  }

  .help.swiper-horizontal>.help.swiper-scrollbar, .help.swiper-scrollbar.swiper-scrollbar-horizontal {
    background-color: #d9e4fabf;
  }

  .help.block-accent .swiper-scrollbar-drag {
    background-color: var(--white);
  }

  .help .swiper-scrollbar-drag {
    background-color: var(--accent);
  }
}