 .scroll-top {
    width: 40px;
    height: 40px;
    background: url(./../img/icons/up-white.svg) center no-repeat var(--accent);
    position: fixed;
    left: 4.3vw;
    bottom: 100px;
    z-index: 999;
    border-radius: 4px;
    border: 1px solid var(--white);
    opacity: 0;
    transition: opacity 0.7s;
    cursor: pointer;
}

@media (max-width: 768px) {
    .scroll-top {
        bottom: 50px;
    }
}

// popups

.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  -webkit-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: #2527347a;
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0 12px;
}

.popup__body_burger {
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 0;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  width: 100%;
  max-width: 823px;
  position: relative;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  padding: 40px;
  background-color: var(--white);
}

.popup__content > * {
    position: relative;
    z-index: 2;
}

.popup form {
    padding: 0;
    min-height: 130px;
}

.popup--cities .form, .popup--search .form {
    position: relative;
    margin-bottom: 30px;
    width: 100%;
    min-height: auto;
}

.popup__title {
    margin-bottom: 14px;
    padding: 0 30px;
    width: 100%;
}

.popup__subtitle {
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
}

.popup--search .popup__title, .popup--cities .popup__title {
    padding-left: 0;
    margin-bottom: 0;
    text-align: left;
}

.search__input {
    width: 100%;
    color: var(--black);
    padding-right: 46px;
}

.popup--search .form__label, .popup--cities .form__label {
    position: relative;
    display: block;
    width: 100%;
}

.popup--search .form__label::before, .popup--cities .form__label::before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  right: 0;
  top: 12px;
  background-image: url("./../img/icons/search-input.svg");
}

.popup__mobile {
    display: none;
}

.form__list {
    padding-right: 10px;
    width: 100%;
    max-height: 358px;
    overflow-y: scroll;
}

.popup.open .popup__content {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.popup__city-list {
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
}

.popup__city-item {
    width: 32%;
}

.popup__city-item a:hover {
    color: var(--accent);
}

.popup__city-list li.hide-search {
    display: none;
}

.form__wrapper {
    width: 100%;
    margin-bottom: 15px;
}

.popup__btn {
    width: 100%;
}

.popup__politic {
    font-size: 12px;
    font-weight: 400;
    color: #fff;
}

.popup__close{
    cursor: pointer;
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    top: 18px;
    right: 18px;
    z-index: 1000;
    width: 32px;
    height: 32px;
    background-image: url("./../img/icons/close-popup.svg");
}

.button__ok {
    margin: 0 auto;
    margin-top: 20px;
    width: 137px;
}

/* popup MEDIA */
@media (max-width: 768px) {
    .popup__content {
        padding: 20px;
    }

    .popup__title {
        margin-bottom: 6px;
        padding: 0;
        font-size: 24px;
        line-height: 110%;
    }

    .popup__subtitle {
        margin-bottom: 17px;
    }

    .popup__close{
        top: 12px;
        right: 12px;
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
    }

    .popup--search .popup__close, .popup--cities .popup__close {
        top: 42px;
    }

    .popup--search.open .popup__content, .popup--cities.open .popup__content {
        height: 100vh;
    }

    .popup__mobile {
        display: block;
        padding-top: 20px;
        padding-bottom: 14px;
        margin-bottom: 14px;
        width: 100%;
        border-bottom: 1px solid var(--border);

        & .logo {
            height: auto;
            width: auto;
        }
    }
    
    .popup--search .popup__body, .popup--cities .popup__body {
        padding: 0 48px 0 0;
        min-height: auto;
    }

    .popup--search .form__label::before, .popup--cities .form__label::before {
        width: 20px;
        height: 20px;
        background-size: 20px 20px;
    }
    .close-popup {
        width: 24px;
        height: 24px;
        top: 10px;
        right: 10px;
    }

    .form__list {
        max-height: 100%;
    }

    .popup__city-list {
        max-height: 500px;
        gap: 20px;
    }

    .popup__city-item {
        width: 100%;
    }
}