.partners {
  padding: 80px 0 120px 0;
  background-image: url("./../img/sections/partners/background.webp");

  & .subtitle {
    margin-bottom: 66px;
  }

  &__slider {
    position: relative;
  }

  &__swiper {
    overflow: hidden;
  }
}

.partner {
  padding: 33px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
  min-height: 267px;
  text-align: center;
  color: var(--text-gray);
  background-color: var(--white);
}

@media (max-width: 768px) {
  .partners {
    padding: 40px 0;
    background-image: none;

    & .subtitle {
      margin-bottom: 28px;
    }

    &__slider {
      padding-bottom: 38px;
    }
  }
}