// reviews-page

.reviews-page {
  padding: 80px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 416px 1fr;
    gap: 41px;
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__icon {
    stroke: var(--accent);
  }

  &__title {
    text-align: left;
  }

  &__tabs {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  & .services-list__item::before {
    display: none;
  }

  & .review {
    min-height: auto;
  }
}

.review__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.reviews-page {
  & .consultation {
    margin-top: 160px;
  }

  & .consultation__wrapper {
    display: flex;
  }

  & .consultation__title {
    text-align: left;
  }

  & .consultation__img {
    margin-right: 36px;
  }

  & .consultation__flex {
    width: 67%;
  }

  & .form {
    padding: 40px;
    min-height: 334px;
  }

  & .form__label {
    width: 48%;
  }

  & .rating__text {
    width: 100%;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    color: var(--black);
  }
}

.rating--form {
  margin-bottom: 0;
  padding: 14px;
  flex-wrap: wrap;
  height: 84px;
  background-color: var(--background);

  & span {
    cursor: pointer;
  }
}

@media (max-width: 768px) {
  .reviews-page {
    padding: 40px 0;

    &__wrapper {
      display: flex;
      flex-direction: column;
    }
  }

  .reviews-page {
    & .consultation {
      margin-top: 80px;
    }

    & .consultation__img {
      display: none;
    }

    & .consultation__flex {
      width: 100%;
    }

    & .form {
      padding: 12px;
      min-height: auto;
    }

    & .form__label {
      width: 100%;
    }
  }
}