.not-found {
  padding: 30px 0 215px 0;
  height: 650px;
  background: #f7f7f7 url("./../img/sections/intro/background-2.png");

  & .breadcrumbs {
    margin-bottom: 116px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__link {
    margin-top: 60px;
    margin-right: auto;
    display: block;
  }

  &__404 {
    margin-right: 20px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 330px;
    color: #91b5ff;
  }
}

@media (max-width: 1300px) {
  .not-found__text {
    width: 50%;
  }
}

@media (max-width: 1023px) {
  .not-found__404 {
    font-size: 200px;
  }
}

@media (max-width: 768px) {
  .not-found {
    padding: 10px 0 140px 0;
    height: auto;
    background-size: auto 50%;
    background-position: top right;
    background-repeat: no-repeat;

    & .breadcrumbs {
      margin-bottom: 30px;
    }

    &__wrapper {
      flex-direction: column;
    }

    &__text {
      width: 100%;
    }

    &__link {
      margin-top: 40px;
      margin-right: 0;
    }

    &__404 {
      margin-bottom: 30px;
      margin-right: 0;
      font-size: 170px;
      line-height: 120%;
      order: -1;
    }
  }
}