.head-doctor {
  position: relative;
  padding: 0 174px 0 140px;
  background: var(--background-small) url("./../img/sections/head-doctor/background.webp");
  background-size: 100% auto;
  background-attachment: fixed;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #0000007a;
    backdrop-filter: blur(8px);
    z-index: 0;
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: 1;
  }

  &__flex {
    padding-top: 80px;
    align-self: flex-start;
    width: 60%;
  }

  &__title {
    margin-bottom: 26px;
    color: var(--white);
    text-align: left;
  }

  &__text {
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 136%;
    font-style: italic;
    color: var(--white);
  }

  &__bage {
    position: absolute;
    bottom: 80px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    width: 578px;
    min-height: 216px;
    background-color: var(--white);
  }

  &__bage::before {
    content: url("./../img/icons/head-doctor-stamp.svg");
    position: absolute;
    width: 88px;
    height: 88px;
    top: 16px;
    right: 12px;
  }

  &__name {
    margin-bottom: 2px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  &__prof {
    margin-bottom: 14px;
    font-size: 14px;
    font-weight: 700;
    color: var(--accent);
  }

  &__work {
    color: var(--text-gray);
  }

  &__buttons {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
  }

  &__button {
    width: 48%;
  }
}

@media (max-width: 1640px) {
  .head-doctor {
    padding: 0;
  }
}

@media (max-width: 1300px) {

  .head-doctor__flex {
    padding-top: 30px;
    width: 100%;
  }

  .head-doctor__text {
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 138%;
  }

  .head-doctor__bage {
    bottom: 30px;
  }

  .head-doctor__img {
    width: 477px;
    height: auto;
  }
}

@media (max-width: 1200px) {
  .head-doctor__title {
    margin-bottom: 20px;
  }

  .head-doctor__img {
    width: 420px;
    padding-top: 85px;
  }

  .head-doctor__bage {
    min-height: auto;
  }

  .head-doctor__buttons {
    margin-top: 40px;
  }
}

@media (max-width: 1024px) {

  .head-doctor {
    padding: 40px 0;

    &__wrapper {
      flex-direction: column;
      gap: 0;
    }

    &__flex {
      padding-top: 0;
      width: 100%;
    }

    &__text {
      margin-bottom: 6px;
    }

    &__img {
      padding-top: 0;
      margin: 0 auto;
      width: 296px;
      height: auto;
    }

    &__bage {
      position: static;
      margin-top: -23px;
      width: 100%;
      order: 1;
    }

    &__bage::before {
      content: "";
    }

    &__name {
      margin-bottom: 4px;
    }

    &__prof {
      line-height: 140%;
    }

    &__buttons {
      flex-direction: column;
      gap: 8px;
    }

    &__button {
      width: 100%;
    }
  }
}