// services-page

.services-page {
  padding: 80px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 416px 1fr;
    gap: 41px;
  }

  &__flex {
    display: flex;
    flex-direction: column;
  }

  &__icon {
    stroke: var(--accent);
  }

  &__title {
    text-align: left;
    margin-bottom: 40px;
  }

  &__list {
    margin-bottom: 40px;
  }
}

.services-list {
  display: none;
}

.services-list.active {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);
  box-shadow: 0px 0px 21.4px 0px #0000000f;
}

.services-list__item {
  position: relative;
  padding: 24px 50px 24px 32px;
  width: 100%;
  border-bottom: 1px solid var(--border);
}

.services-list__item:last-child {
  border-bottom: none;
}

.services-list__item:nth-child(even) {
  background-color: var(--background);
}

.services-list__title {
  font-size: 20px;
  font-weight: 700;
}

.services-tabs {
  margin-bottom: auto;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);
  box-shadow: 0px 0px 21.4px 0px #0000000f;
}

.services-tab {
  position: relative;
  padding: 30px;
  width: 100%;
  min-height: 86px;
  text-align: left;
  border-bottom: 1px solid var(--border);

  &:last-child {
    border-bottom: none;
  }

  & p {
    font-size: 14px;
  }
}

.services-tab::before,
.services-list__item::before {
  content: "";
  position: absolute;
  right: 30px;
  top: 30px;
  display: block;
  width: 26px;
  height: 26px;
  background: url("./../img/icons/down.svg") no-repeat;
  transform: rotate(-90deg);
  transform-origin: center;
  transition: .3s;
}

.services-list__item::before {
  right: 25px;
  top: 25px;
}

.tab--active {
  color: var(--white);
  background-color: var(--accent);
  transition: background-color 0.5s;
  cursor: default;

  &:hover {
    background-color: var(--accent);
  }
}

.tab--active .services-page__icon {
  stroke: var(--white);
}

.tab--active.services-tab::before {
  background: url("./../img/icons/right-white.svg") no-repeat;
}

.services-page__tabs .services-tab {
  padding: 20px;
}

.services-page__tab-name {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 700;
  font-size: 20px;
  line-height: 140%;
}

.popular {
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 40px;
  color: var(--white);
  background: #fe7c45 url("./../img/sections/intro/background-small.webp");

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
  }

  &__item {
    position: relative;
    padding: 20px 40px 20px 20px;
    width: 48%;
    background-color: #ffffff33;

    & a {
      font-weight: 700;
      font-size: 20px;
    }

    & a:hover {
      color: var(--black);
    }

    &::before {
      content: "";
      position: absolute;
      right: 20px;
      top: 20px;
      display: block;
      width: 26px;
      height: 26px;
      background: url("./../img/icons/right-white.svg") no-repeat;
      transform: rotate(-90deg);
      transform-origin: center;
      transition: .3s;
    }
  }
}

.text-content__services {
  padding: 40px;
  border: 1px solid var(--border);
  background-color: var(--background);
}

@media (max-width: 1023px) {
  .services-page {
    padding: 40px 0;

    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__title {
      margin-bottom: 20px;
    }
  }

  .services-list__item {
    padding: 18px 40px 18px 14px;
  }

  .services-list__title {
    font-size: 16px;
  }

  .services-tabs {
    margin-bottom: 0;
  }

  .services-tab {
    padding: 20px;
    width: 100%;
  }

  .services-tab::before {
    display: none;
  }

  .services-tab.tab--active::before {
    content: "";
    position: absolute;
    right: 20px;
    top: 20px;
    display: block;
    width: 26px;
    height: 26px;
    background: url("./../img/icons/right-white.svg") no-repeat;
    transform: rotate(180deg);
    transform-origin: center;
    transition: .3s;
  }

  .expanded .services-tab.tab--active::before {
    transform: rotate(180deg);
  }

  .services-list__item::before {
    right: 16px;
    top: 16px;
  }

  .services-page__tab-name {
    margin-bottom: 14px;
    gap: 8px;
    font-size: 18px;

    & svg {
      width: 18px;
      height: 18px;
    }
  }

  .popular {
    padding: 20px;
    gap: 20px;

    &__list {
      flex-direction: column;
    }

    &__item {
      position: relative;
      padding: 16px 40px 16px 16px;
      width: 100%;

      & a {
        font-size: 16px;
      }

      &::before {
        right: 16px;
        top: 45%;
        display: block;
        width: 22px;
        height: 22px;
        background-size: 22px 22px;
      }
    }
  }

  .text-content__services {
    padding: 14px;
  }
}

.call-doctor {
  padding: 80px 0;

  &__wrapper {
    padding: 40px;
    display: flex;
    justify-content: space-between;
    background-image: url("./../img/sections/intro/background-small.webp");
    background-size: auto 100%;
  }

  &__title {
    margin-bottom: 6px;
    text-align: left;
    width: 40%;
  }

  &__flex {
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__phone {
    position: relative;
    padding-left: 50px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 120%;

    &::before {
      content: "";
      position: absolute;
      width: 36px;
      height: 36px;
      left: 0;
      top: 4px;
      background: url("./../img/icons/phone-white.svg");
    }
  }

  &__button {
    font-weight: 700;
    width: 183px;
  }
}

.call-doctor--services .call-doctor__title {
  width: 100%;
}

@media (max-width: 1500px) {
  .call-doctor__flex {
    width: 50%;
  }
}

@media (max-width: 1220px) {
  .call-doctor__title {
    width: 80%;
  }
}

@media (max-width: 1050px) {
  .call-doctor__flex {
    width: 78%;
  }
  
  .call-doctor__wrapper {
    padding: 20px;
  }
  
  .call-doctor__flex {
    flex-direction: column;
  }

  .call-doctor__title {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .call-doctor {
    padding: 40px 0;

    &__wrapper {
      flex-direction: column;
      gap: 30px;
    }

    &__flex {
      width: 100%;
    }

    &__phone {
      margin-top: 30px;
      padding-left: 32px;
      font-size: 20px;

      &::before {
        width: 18px;
        height: 18px;
        left: 0;
        top: 3px;
        background-size: 18px 18px;
      }
    }

    &__note {
      margin-bottom: 20px;
      padding-left: 32px;
      font-size: 14px;
    }

    &__button {
      width: 100%;
    }
  }
}