.text-content {
  padding: 52px 0 80px 0;

  &--main {
    background-color: var(--background);
  }

  &__wrapper {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    max-width: 1120px;
  }

  & h2,
  h3,
  h4,
  h5 {
    margin-top: 28px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    line-height: 120%;
  }

  & h2 {
    margin-bottom: 27px;
    font-size: 46px;
  }

  & h3 {
    margin-bottom: 21px;
    font-size: 36px;
  }

  & h4 {
    margin-bottom: 24px;
    margin-top: 19px;
    font-size: 30px;
  }

  & h5 {
    margin-bottom: 17px;
    font-size: 20px;
  }

  & p {
    margin-bottom: 20px;
  }

  & p:last-child {
    margin-bottom: 0;
  }

  & ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  & ul li {
    position: relative;
    padding-left: 17px;
  }

  & ul li::before {
    content: "";
    width: 6px;
    height: 6px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background-color: var(--accent);
    border-radius: 50%;
  }

  & ol {
    margin-bottom: 20px;
    list-style: decimal;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  & ol li {
    position: relative;
    padding-left: 30px;
  }

  & ol li::marker {
    content: none;
  }

  & ol li::before {
    content: counter(list-item);
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: var(--accent);
    color: white;
    font-size: 14px;
    font-weight: 700;
    line-height: 90px;
  }

  table {
    margin-bottom: 20px;
    width: 100%;
    white-space: nowrap;
    border-collapse: collapse;
    text-align: left;
  }

  table th,
  td {
    padding: 16px;
    border: 1px solid var(--background-small);
  }

  table th {
    background-color: #e5edf7;
  }

  table tr:nth-child(2n) {
    background-color: #e5edf780;
  }
}

.consultation {
  margin: 28px 0 20px 0;
  padding: 0 36px;
  color: var(--white);
  background: var(--accent) url("./../img/sections/consultation/background.webp");

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__flex {
    width: 59%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    margin-bottom: 6px;
  }
}

p.consultation__subtitle {
  margin: 0;
}

.form {
  margin-top: 30px;
  padding: 20px 20px 18px 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 18px 20px;
  font-size: 12px;
  color: var(--text-light);
  background-color: var(--white);

  &__label {
    position: relative;
    width: 48%;
  }

  &__label--agree {
    display: flex;
    gap: 8px;
  }

  &__agree {
    width: 96%;
  }

  &__input {
    width: 100%;

    &:focus {
      border-color: var(--black);
    }

    &::placeholder {
      color: #2b547480;
    }
  }

  &__button {
    width: 100%;
    height: 54px;
    font-family: 700;
  }
}

.form--inverse {
  padding: 0;
  background-color: transparent;
  color: var(--white);
}

.consultation--no-picture {
  padding: 40px;

  & .consultation__wrapper {
    flex-direction: column;
    align-items: center;
    gap: 0 14px;
  }

  & .form__label {
    width: 31%;
  }

  & .form__label--agree {
    margin: 0 auto;
    width: max-content;
    text-align: center;
  }

  & .form__input {
    color: var(--white);
    border-color: var(--white);

    &::placeholder {
      color: var(--white);
      border-color: var(--white);
    }

    &:focus {
      border-color: var(--white);
    }
  }

  & input[type="checkbox"] {
    background-color: #f3f4f6;
  }
}

.check {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;

  & p {
    margin: 0;
  }

  &__doctor {
    padding: 20px;
    width: 49%;
    display: flex;
    flex-direction: column;
    gap: 18px;
    color: var(--text-gray);
    border: 1px solid var(--border);
  }

  &__wrapper {
    display: flex;
    gap: 16px;
  }

  &__note {
    margin-right: auto;
    margin-bottom: 6px;
    padding: 4px 8px;
    display: inline-block;
    color: #193cb8;
    background-color: var(--background-small);
  }

  &__name {
    font-weight: 700;
    font-size: 20px;
    color: var(--black);
  }

  &__prof {
    margin-bottom: 10px;
    font-size: 14px;
  }

  &__inner {
    display: flex;
    gap: 16px;

    & span {
      position: relative;
      padding-left: 18px;
      font-size: 14px;
    }
  }

  &__work::before,
  &__date::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    left: 0;
    top: 3px;
  }

  &__work::before {
    background: url("./../img/icons/stamp-gray.svg");
  }

  &__date::before {
    background: url("./../img/icons/date-gray.svg");
  }

  &__comment {
    padding: 14px;
    font-size: 14px;
    line-height: 20px;
    font-style: italic;
    background-color: #f9fafb;
  }
}

.check__doctor--right {
  & .check__note {
    color: #016630;
    background-color: #dcfce7;
  }

  & .check__comment {
    background-color: #f0fdf4;
  }
}

@media (max-width: 1640px) {
  .text-content__wrapper {
    padding: 0 12px;
  }

  .text-content blockquote {
    margin-left: 19px;
  }
}

@media (max-width: 1076px) {
  .consultation {
    padding: 20px;

    &__img {
      display: none;
    }

    &__flex {
      width: 100%;
    }
  }
}

@media (max-width: 768px) {
  .text-content {
    padding: 23px 0 43px 0;

    & h2,
    h3,
    h4,
    h5 {
      margin-top: 17px;
      line-height: 110%;
    }

    & h2 {
      margin-bottom: 20px;
      font-size: 28px;
    }

    & h3 {
      margin-bottom: 14px;
      font-size: 24px;
    }

    & h4 {
      margin-bottom: 14px;
      font-size: 20px;
    }

    & h5 {
      margin-bottom: 14px;
      font-size: 18px;
    }

    & p {
      margin-bottom: 14px;
      line-height: 137%;
    }

    & table {
      margin-top: 10px;
      margin-bottom: 14px;
      padding-bottom: 21px;
      display: block;
      overflow-x: auto;
    }

    & ul {
      gap: 6px;
    }

    & ul li {
      position: relative;
      padding-left: 17px;
    }

    & blockquote {
      margin: 36px 0 14px 10px;
      padding: 19px;

      & p {
        margin-bottom: 9px;
        font-size: 16px;
      }

      & span {
        font-size: 14px;
        line-height: 110%;
      }
    }

    &__warning {
      margin: 36px 0 14px 0;
      padding: 20px;
      gap: 13px;

      &::before {
        width: 26px;
        height: 26px;
        left: 20px;
        top: 20px;
        background-size: 15px 15px;
      }

      & b {
        display: block;
        padding-left: 40px;
      }
    }
  }

  .consultation {
    margin-top: 16px;
    margin-bottom: 14px;

      &__form {
      min-height: 160px;
    }
  }

  .form {
    margin-top: 15px;
    padding: 12px;
    flex-direction: column;
    gap: 14px;
    min-height: auto;
    line-height: 110%;

    &__label {
      position: relative;
      width: 100%;
    }

    &__agree {
      line-height: 144%;
    }

    &__button {
      margin-top: 6px;
    }
  }

  .consultation--no-picture {
    padding: 20px;

    & .consultation__title {
      text-align: left;
    }

    & .consultation__subtitle {
      margin-bottom: 30px;
    }

    // & .consultation__wrapper {
    //   flex-direction: column;
    //   align-items: center;
    //   gap: 0 14px;
    // }

    & .form {
      margin: 0;
      padding: 0;
    }

    & .form__label {
      width: 100%;
    }

    & .form__label--agree {
      margin: 0 auto;
      text-align: center;
    }

    & .form__input {
      color: var(--white);
      border-color: var(--white);

      &::placeholder {
        color: var(--white);
        border-color: var(--white);
      }

      &:focus {
        border-color: var(--white);
      }
    }

    & input[type="checkbox"] {
      background-color: #f3f4f6;
    }
  }

  .check {
    margin-top: 16px;
    flex-direction: column;
    gap: 20px;

    &__doctor {
      padding: 14px;
      width: 100%;
    }

    &__img {
      width: 58px;
      height: 58px;
    }

    &__note {
      margin-bottom: 10px;
      font-size: 14px;
    }

    &__name {
      font-size: 18px;
    }

    &__inner {
      gap: 14px;
    }
  }
}