// services-page

.prices-page--mobile {
  display: none;
}

.prices-page {
  padding: 80px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 416px 1fr;
    gap: 41px;
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  &__icon {
    stroke: var(--accent);
  }

  &__title {
    text-align: left;
  }

  &__tabs {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    box-shadow: 0px 0px 21.4px 0px #0000000f;
  }
}

.price {
  display: block;
  transition: 0.3s;
  overflow: hidden;

  &[open] {
    padding-bottom: 100px;

    & .services-list__item {
      border: none;
    }

    & .services-list__item::before {
      transform: rotate(360deg);
    }
  }

  &__content {
    margin: 0 24px;
    padding-top: 24px;
    border-top: 1px solid var(--border);
    font-size: 16px;
    margin-bottom: -69px;
  }

  &__title {
    padding-right: 86px;
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 700;
    transition: .3s;
    cursor: pointer;

    &::marker {
      content: '';
    }

    &::-webkit-details-marker {
      display: none;
    }

    & span {
      font-family: "PTSerif", serif;
      font-weight: 700;
      font-size: 20px;
      line-height: 110%;
      text-align: right;
      text-decoration: underline;
      color: var(--accent);
    }
  }
}

.prices-page__tab {
  margin-bottom: auto;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);
}

@media (max-width: 1023px) {
  .prices-page--mobile {
    display: block;
  }

  .prices-page--desktop {
    display: none;
  }

  .prices-page {
    padding: 40px 0;

    &__wrapper {
      display: flex;
      flex-direction: column;
      gap: 0;
    }
  }

  .price__title {
    padding: 0;
  }

  .price__content {
    margin: 0;
    padding-top: 8px;
    border: none;
    font-size: 16px;
    color: var(--text-gray);
  }

  .prices-page__tab {
    position: relative;
    width: 100%;
    min-height: auto;
    text-align: left;
    border-bottom: 1px solid var(--border);

    &::before {
      content: "";
      position: absolute;
      right: 20px;
      top: 20px;
      display: block;
      width: 26px;
      height: 26px;
      background: url("./../img/icons/down.svg") no-repeat;
      transform-origin: center;
      transition: .3s;
    }

    &[open] {
      position: relative;
      padding-bottom: 68px;

      & .prices-page__summary {
        background-color: var(--accent);
        color: var(--white);
      }

      &.prices-page__tab::before {
        background: url("./../img/icons/down-white.svg");
        transform: rotate(-180deg);
        transform-origin: center;
        transition: .3s;
      }
    }
  }

  .prices-page__list {
    margin-bottom: -69px;
  }

  .prices-page__summary {
    padding: 23px 20px 23px 20px;
    font-family: "PTSerif", serif;
    font-size: 18px;
    line-height: 110%;

    &::marker {
      content: '';
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  .prices-page .services-list__item {
    padding: 20px 24px;
  }

  .prices-page .services-list__item::before {
    display: none;
  }
}