.about-page {
  padding-top: 80px;
  background-color: var(--background);

  &__wrapper {
    display: flex;
    gap: 5%;
  }

  &__title {
    margin: 100px 0 40px 0;
    text-align: left;
  }

  & .about__flex {
    width: 71%;
  }

  & .about__list {
    margin-top: 40px;
    width: 100%;
    flex-direction: row;
  }

  & .about__item {
    width: 32%;
    min-height: auto;
  }

  & blockquote {
    margin-top: 0;
  }

  &__text p {
    margin-bottom: 24px;
  }

  .doctors__wrapper {
    position: relative;
  }

  .subtitle {
    text-align: left;
  }

  .doctors__title {
    text-align: left;
  }

  .doctors__link {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.values {
  padding-top: 120px;

  &__title {
    margin-bottom: 40px;
    text-align: center;
  }

  &__icon {
    margin-left: auto;
    text-align: left;
  }

  &__name {
    margin-top: auto;
    margin-bottom: 14px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 110%;
  }
}

ul.values__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 20px;
}

li.values__item {
  padding: 36px 24px 24px 24px;
  width: 48%;
  min-height: 168px;
  display: flex;
  flex-direction: column;
  border-left: 4px solid var(--accent);
  box-shadow: 0px 10px 15px -3px #0000001a;
}

.numbers {
  padding: 80px 0;
  background: var(--background) url("./../img/sections/intro/background-small.webp") no-repeat;
  background-size: auto 100%;
  background-position: center;

  &__title {
    margin-bottom: 60px;
  }
}

ul.numbers__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
}

li.numbers__item {
  padding: 20px;
  width: 24%;
  height: auto;
  min-height: 144px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 24px;
  text-align: center;
  background-color: var(--white);

  & span {
    color: var(--accent);
  }

  & p {
    font-size: 18px;
  }
}

.call-doctor--with-form {
  padding: 43px;
  background-image: url("./../img/sections/intro/background-small.webp");
  background-repeat: no-repeat;
  background-size: auto 100%;

  .subtitle {
    margin-bottom: 0;
    text-align: left;
  }

  .form__label {
    width: 32%;
  }

  .call-doctor {
    &__wrapper {
      padding: 0;
      background-image: none;
    }

    &__title {
      width: 70%;
    }

    &__flex {
      flex-direction: column;
      justify-content: center;
      gap: 5px;
    }
  }

}

.consultation--sidebar {
  margin-top: 0;
  margin-bottom: auto;
  padding: 30px;
  width: 395px;

  .consultation {
    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__title {
      text-align: left;
      font-size: 30px;
    }
  }

  & .form__label {
    width: 100%;
  }
}

@media (max-width: 1640px) {
  li.numbers__item {
    width: 23%;
  }
}

@media (max-width: 1300px) {
  li.numbers__item {
    width: 48%;
  }
}

@media (max-width: 1023px) {
  .about-page {
    & .about__flex {
      width: 65%;
    }
  }

  li.values__item {
    width: 47%;
  }

  .consultation--sidebar {
    display: none;
  }

  .about-page .about__wrapper {
    display: block;
  }

  .about-page .about__flex {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .about-page {
    padding-top: 40px;

    &__title {
      margin: 20px 0 20px 0;
    }

    & .about__list {
      margin-top: 20px;
      flex-direction: row;
      gap: 9px;

      & span {
        margin: 0;
        border: none;
      }
    }

    & .about__item {
      padding: 20px 15px;
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 8px;
      text-align: center;
      font-size: 16px;

      & span {
        font-size: 32px;
      }
    }

    &__text p {
      margin-bottom: 14px;
    }

    .subtitle {
      text-align: center;
    }

    .doctors__title {
      text-align: center;
    }

    .doctors__link {
      position: static;
    }
  }

  .values {
    padding-top: 80px;

    &__title {
      margin-bottom: 45px;
    }

    &__icon {
      margin-left: auto;
      margin-bottom: 18px;
    }

    &__name {
      margin-top: auto;
      margin-bottom: 14px;
      font-family: "PTSerif", serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 110%;
    }
  }

  ul.values__list {
    flex-direction: column;
    gap: 10px;
  }

  li.values__item {
    padding: 36px 36px 24px 24px;
    width: 100%;
    min-height: auto;
  }

  .numbers {
    padding: 40px 0;
    background-size: auto 25%;
    background-position: top right;

    &__title {
      margin-bottom: 30px;
    }
  }

  ul.numbers__list {
    flex-direction: column;
    align-items: center;
  }

  li.numbers__item {
    width: 100%;
    min-height: 103px;
    gap: 6px;

    & p {
      font-size: 32px;
    }

    & p {
      font-size: 16px;
    }
  }

  .call-doctor--with-form {
    padding: 20px;
    background-size: auto 25%;
    background-repeat: repeat;

    .subtitle {
      display: none;
    }

    .form__label {
      width: 100%;
    }

    .call-doctor {
      &__wrapper {
        padding: 0;
        background-image: none;
      }

      &__title {
        margin: 0;
        width: 100%;
      }

      &__flex {
        flex-direction: column;
        align-items: flex-start;
        gap: 0;
      }

      &__phone {
        margin-top: 0;
      }
    }

  }
}