// footer

.footer {
  padding: 80px 0 40px 0;
  color: #ffffff88;
  background-color: #101828;

  &__wrapper {
    margin-bottom: 40px;
    padding: 40px;
    display: flex;
    justify-content: space-between;
    border: 1px solid #ffffff33;
  }

  &__logo {
    color: var(--white);
  }

  &__phone {
    margin-top: 8px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--white);

    &:hover {
      color: var(--accent);
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 14px;

    & .label {
      width: 100%;
    }

    & .input {
      color: var(--white);

      &:focus {
        border-color: var(--white);
      }
    }

    input[type="checkbox"] {
      margin-right: 5px;
      background-color: #f3f4f622;
    }

    & .button {
      margin-top: 6px;
    }
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 50px;
    width: 419px;
  }
}

.pay {
  padding: 20px;
  color: var(--white);
  background-color: #ffffff0a;

  &__title {
    margin-bottom: 16px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 6px;
  }

  &__item {
    width: 32%;
    display: flex;
    gap: 2px;
    align-items: center;
    font-size: 12px;
  }
}

.footer__flex--nav {
  display: flex;
  justify-content: space-between;
  width: 65%;

  & a:hover {
    color: var(--white);
  }
}

.footer-services__title, .footer-navigation__title {
    margin-bottom: 20px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    color: var(--white);
  }

.footer-services {
  width: 640px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
  }

  &__item {
    width: 49%;
  }
}

.footer-navigation {
  width: 30%;

  &__list {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  & address {
    font-style: normal;
  }

  & span {
    margin-bottom: 40px;
    display: block;
    font-weight: 700;
    color: var(--white);
  }

  &__docs {
    font-size: 14px;
  }
}

.socials {
  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__link:hover {
    opacity: 0.7;
  }
}

.footer__bottom {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  background-color: #fafafa0a;
  color: var(--white);

  & p {
    width: 45%;
  }

  &-copy {
    text-align: right;
  }
}

@media (max-width: 1440px) {
  .footer-services {
    width: 75%;
  }
}

@media (max-width: 1350px) {
  .footer__flex--nav {
    width: 60%;
  }
}

@media (max-width: 1280px) {
  .footer__flex--nav {
    width: 50%;
  }
  
  .footer-services__item {
    width: 100%;
  }

  .footer-navigation {
    width: 50%;
  }
}

@media (max-width: 1024px) {

  .footer {
    padding: 12px 0;

    &__wrapper {
      margin-bottom: 40px;
      padding: 20px;
      flex-direction: column;
      gap: 80px;
    }

    &__phone {
      font-size: 24px;
      line-height: 110%;
      color: var(--white);
    }

    &__flex {
      gap: 40px;
      width: 100%;
    }
  }

  .pay {
    padding: 16px;

    &__title {
      font-size: 18px;
      line-height: 110%;
    }

    &__list {
      gap: 7px;
    }

    &__item {
      width: auto;
      font-size: 10px;
    }
  }

  .footer__flex--nav {
    flex-direction: column;
    gap: 40px;
    width: 100%;
  }

  .footer-services__title, .footer-navigation__title {
      font-size: 16px;
      line-height: 110%;
    }

  .footer-services {

    &__list {
      flex-direction: column;
    }

    &__item {
      width: 100%;
    }
  }

  .footer-navigation {
    width: 100%;
  }

  .footer__bottom {
    padding: 20px;
    flex-direction: column;
    gap: 20px;

    & p {
      width: 100%;
    }

    &-copy {
      text-align: left;
    }
  }
}