// guarantees

.guarantees {
  padding: 80px 0 88px 0;

  &--main {
    background-image: url("./../img/sections/guarantees/background.webp");
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.guarantee {
  width: 17%;
  display: flex;
  flex-direction: column;
  gap: 24px;
  text-align: center;

  &__icon {
    margin: 0 auto;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--accent);
    box-shadow: 0px 4px 6px -4px #0000001a, 0px 10px 15px -3px #0000001a;
  }

  &__note {
    padding: 0 16px;
  }
}

.guarantees--main .guarantee__icon {
  background-color: var(--white);
}

.guarantees .subtitle {
  margin-top: 14px;
}

@media (max-width: 768px) {
  .guarantees {
    padding: 40px 0;
    background-image: none;

    &__list {
      flex-direction: column;
      gap: 30px;
    }
    
  & .subtitle {
    margin-top: 9px;
    margin-bottom: 31px;
  }
}

  .guarantee {
    width: 100%;
    display: grid;
    grid-template-columns: 60px 1fr;
    align-items: center;
    gap: 10px;
    text-align: left;

    &__icon {
      margin: 0;
      width: 60px;
      height: 60px;

      & img {
        width: 30px;
        height: 30px;
      }
    }

    &__note {
      padding: 0;
    }
  }
}