// about

.about {
  padding: 120px 0 80px 0;
  background-color: var(--background);

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    margin-bottom: 40px;
    text-align: left;
  }

  & p {
    margin-bottom: 24px;
  }

  &__lead {
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 700;
    line-height: 140%;
  }

  &__subtitle {
    margin-top: 16px;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: var(--accent);
  }

  &__flex {
    width: 62%;
    display: flex;
    flex-direction: column;

    & ul {
      flex-direction: row;
      gap: 8px;
      justify-content: space-between;
    }

    & li {
      width: 47%;
    }
  }

  &__list {
    width: 27vw;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    text-align: center;

    & span {
      margin-bottom: 8px;
      display: flex;
      justify-content: center;
    }
  }
}

.about__item {
  padding: 27px 24px 24px 24px;
  width: 49%;
  min-height: 162px;
  background-color: var(--accent);
  color: var(--white);
}

@media (max-width: 1850px) {
  .about .text-content {
    margin-right: 30px;
  }

  .about__list {
    width: 38%;
  }
}

@media (max-width: 1400px) {
  .about__flex ul {
    margin-top: -4px;
    flex-direction: column;
    gap: 5px;
  }

  .about__flex li {
    width: 100%;
  }

  .about__item {
    padding: 16px 14px;
    width: 100%;
    min-height: 115px;
  }

  .about__list span {
    font-size: 28px;
  }
}

@media (max-width: 768px) {
  .about {
    padding: 40px 0;

    &__wrapper {
      flex-direction: column;
    }

    &__lead {
      margin-bottom: 14px;
      font-size: 16px;
      line-height: 138%;
    }

    &__subtitle {
      margin-top: 16px;
      margin-bottom: 18px;
      font-size: 18px;
    }

    &__flex {
      width: 100%;
    }

    &__list {
      margin-top: 42px;
      width: 100%;
      flex-direction: column;
      gap: 4px;
      text-align: left;

      & span {
        margin-bottom: 0;
        margin-right: 10px;
        align-items: center;
        border-right: 1px solid var(--border);
      }
    }
  }

  .about__item {
    display: grid;
    grid-template-columns: 109px 1fr;
    min-height: 76px;
    font-size: 13px;
  }
}