.contacts {
  padding: 120px 0;

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }

  &__phone {
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--accent);

    &:hover {
      color: var(--black);
    }
  }

  &__item {
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 32%;
    background-color: var(--background);
    color: var(--text-gray);

    & address {
      font-style: normal;
    }

    & span {
      font-size: 20px;
      font-weight: 700;
      color: var(--black);
    }
  }
  
  &__map {
    position: relative;
    margin-bottom: 80px;
    width: 100%;
    height: 488px;
  }

  &__plashka {
    position: absolute;
    padding: 26px 24px 26px 81px;
    left: 30px;
    bottom: 30px;
    width: 326px;
    font-weight: 700;
    background: var(--background)url("./../img/icons/map.svg") top 24px left 24px no-repeat;
    box-shadow: 0px 4px 6px -4px #0000001a, 0px 10px 15px -3px #0000001a;
    z-index: 2;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__subtitle {
    margin-bottom: 24px;
    padding-bottom: 80px;
    width: 100%;
    border-bottom: 1px solid var(--border);
    text-align: center;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 120%;

    & span {
      display: block;
      color: var(--accent);
    }
  }

  &__flex {
    padding: 30px;
    width: 49%;
    background-color: var(--background);

    & p {
      margin-bottom: 20px;
      font-family: "PTSerif", serif;
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
    }
  }
}

@media (max-width: 768px) {
  .contacts {
    padding: 40px 0;

    &__title {
      margin-bottom: 30px;
    }

    &__list {
      flex-direction: column;
    }

    &__phone {
      font-size: 20px;
      line-height: 110%;
      color: var(--accent);
    }

    &__item {
      padding: 20px;
      width: 100%;

      & span {
        font-size: 16px;
      }
    }
    
    &__map {
      margin-bottom: 50px;
      width: 100%;
      height: 488px;
    }

    &__plashka {
      position: absolute;
      padding: 14px 16px 14px 59px;
      left: 12px;
      bottom: 12px;
      width: 272px;
      background-position: 15px 14px;
      background-size: 33px 42px;
    }

    &__wrapper {
      flex-direction: column;
      gap: 10px;
    }

    &__subtitle {
      padding-bottom: 50px;
      margin-bottom: 14px;
      font-size: 20px;
      line-height: 110%;
      text-align: left;
    }

    &__flex {
      padding: 20px;
      width: 100%;

      & p {
        margin-bottom: 16px;
        font-size: 18px;
        line-height: 110%;
      }
    }
  }
}