.steps {
  padding: 80px 0;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  & .subtitle {
    margin-bottom: 24px;
  }

  &__notes {
    margin: 0 auto;
    margin-bottom: 64px;
    display: flex;
    gap: 10px;
  }

  &__note {
    padding: 13px 20px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 18px;
    font-weight: 700;
    border: 1px solid var(--border);
  }
}

@media (min-width: 769px) {
  .steps .swiper-wrapper {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}

.step {
  position: relative;
  display: flex;
  width: 100%;
  height: 320px;
  box-shadow: 0px 20px 25px -5px #0000001a;

  &__number {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 16px;
    left: 16px;
    width: 64px;
    height: 64px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    border-radius: 50%;
    background-color: var(--accent);
    color: var(--white);
  }

  &__img {
    width: 50%;
    overflow: hidden;

    & img {
      width: 100%;
      height: auto;
    }
  }

  &__wrapper {
    padding: 40px;
    width: 50%;
    display: flex;
    flex-direction: column;
  }

  &__wrapper-flex {
    display: grid;
    grid-template-columns: 80px 1fr;
    gap: 20px;
  }

  &__icon {
    margin-bottom: 23px;
    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--accent);
  }

  &__name {
    margin-bottom: 6px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
  }

  & span {
    font-size: 16px;
    color: var(--text-gray);
  }

  &__description {
    font-size: 18px;
    color: var(--text-gray);
  }

  &__time {
    position: relative;
    margin-top: auto;
    margin-right: auto;
    padding: 6px 12px 6px 38px;
    background-color: var(--background-small);

    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 12px;
      top: 9px;
      background: url("./../img/icons/clock-black.svg");
    }
  }

  &:nth-child(even) {

    .step__number {
      left: auto;
      right: 16px;
    }

    .step__img {
      order: 2;
    }
  }
}

.ready {
  padding: 32px;
  display: flex;
  flex-direction: column;
  background-image: url("./../img/sections/consultation/background.webp");
  box-shadow: 0px 25px 50px -12px #00000040;

  & .subtitle {
    margin-bottom: 43px;
  }

  &__buttons {
    margin: 0 auto;
    display: flex;
    gap: 12px
  }

  & .button--transparent {
    color: var(--white);
    border-color: var(--white);
  }
}

@media (max-width: 1200px) {
  .step__img {

    & img {
      width: auto;
    }
  }
}

@media (max-width: 768px) {
  .steps {
    padding: 40px 0;

    & .subtitle {
      margin-bottom: 22px;
    }

    &__notes {
      margin: 0;
      margin-bottom: 30px;
      flex-direction: column;
    }

    &__note {
      padding: 12px 20px;
      width: 100%;
      font-size: 16px;
    }

    &__slider {
      position: relative;
      margin-bottom: 26px;
      padding-bottom: 40px;
    }
  }

  .step {
    position: relative;
    flex-direction: column;
    width: 100%;
    height: auto;
    box-shadow: 0px 20px 25px -5px #0000001a;

    &__number {
      left: auto;
      right: 16px;
      width: 40px;
      height: 40px;
      font-size: 18px;
    }

    &__img {
      width: 100%;
      height: 200px;
      display: flex;
      justify-content: center;
      align-items: center;

      & img {
        width: 100%;
      }
    }

    &__wrapper {
      padding: 14px;
      width: 100%;
    }

    &__wrapper-flex {
      margin-bottom: 23px;
      grid-template-columns: 48px 1fr;
      gap: 10px;
    }

    &__icon {
      margin-bottom: 0;
      width: 48px;
      height: 48px;

      & img {
        width: 23px;
        height: 23px;
      }
    }

    &__name {
      margin-bottom: 0;
      font-size: 20px;
      line-height: 110%;
    }

    & span {
      margin-bottom: 23px;
      font-size: 14px;
    }

    &__description {
      margin-bottom: 30px;
      font-size: 16px;
    }

    &:nth-child(even) {
      .step__img {
        order: 0;
      }
    }
  }

  .ready {
    padding: 20px 14px 14px 14px;

    &__title {
      font-size: 24px;
      line-height: 110%;
    }

    & .subtitle {
      margin-bottom: 40px;
    }

    &__buttons {
      margin: 0;
      flex-direction: column;
    }

    &__button {
      width: 100%;
    }
  }

}