.documents {
  padding: 120px 0 80px 0;

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 60px;
  }

  &__slider {
    position: relative;
    padding: 20px 13px 78px 13px;
    width: 669px;
    height: 553px;
    overflow: hidden;
    border: 1px solid var(--border);
  }

  &-slider__navigation {
    left: auto;
    top: auto;
    bottom: 20px;
    width: 96%;
    transform: none;
  }

  &__note {
    margin-top: 26px;
    text-align: center;
    color: var(--text-gray);
  }

  &__flex {
    width: 53%;
  }

  &__title {
    margin-bottom: 40px;
    text-align: left;
  }

  &__text-black {
    margin-bottom: 20px;
  }

  &__text-gray {
    color: var(--text-gray);
  }

  &__link {
    display: block;
    margin-top: 78px;
    width: 179px;
  }

}

.document {
  &__img {
    width: 100%;
    height: auto;
  }
  & a {
    display: block;
  }
}

@media (max-width: 1440px) {
  .documents__wrapper {
    gap: 20px;
  }

  .document {
    &__img {
      margin: 0 auto;
    }
  }
}

@media (max-width: 768px) {
  .documents {
    padding: 40px 0;

    &__wrapper {
      flex-direction: column;
      gap: 40px;
    }

    &__slider {
      padding: 11px;
      width: 100%;
      height: 440px;
    }

    &__note {
      display: none;
    }

    &__flex {
      width: 100%;
      order: -1;
    }

    &__title {
      margin-bottom: 30px;
    }

    &__text-black {
      margin-bottom: 12px;
    }

    &__link {
      margin-top: 30px;
      width: 100%;
    }

    & .swiper-scrollbar {
      margin: 0 11px;
      width: 90%;
      bottom: 18px;
    }
  }

  .document {
    &__img {
      width: 272px;
    }
  }
}

// gallery-page

.documents-page {
  padding: 80px 0;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}

.documents-page__item {
  width: 24%;

  & img {
    width: 100%;
    height: 598px;
    object-fit: cover;
    object-position: center;
    overflow: hidden;
  }
}

@media (max-width: 1640px) {
  .documents-page__item {
    width: 32%;
  }
}

@media (max-width: 1440px) {
  .documents-page__item {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .documents-page {
    padding: 40px 0;

    &__list {
      flex-direction: column;
    }

    .documents-page__item {
      width: 100%;

      & img {
        height: calc(100vh - 50%);
      }
    }
  }
}