// doctors

.doctors {
  padding: 120px 0;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 5px;
  }

  & .subtitle {
    margin-bottom: 48px;
  }

  &__slider {
    position: relative;
    margin-bottom: 50px;
    height: 100%;
  }

  &__swiper {
    overflow: hidden;
  }

  &__link {
    margin: 0 auto;
  }
}

.doctor {
    padding: 20px 24px 24px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    min-height: 385px;
    text-align: center;
    color: var(--text-gray);
    border: 1px solid var(--border);
    border-top: 4px solid var(--accent);

  &__img {
    margin: 0 auto 20px auto;
  }

  &__name {
    margin-bottom: 10px;
    width: 88%;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 110%;
    color: var(--black);
  }

  &__button {
    margin-top: auto;
    width: 100%;
    font-weight: 700;
  }
}

@media (max-width: 768px) {
  .doctors {
    padding: 40px 0;

    & .subtitle {
      margin-bottom: 29px;
      margin-top: 4px;
    }

    &__slider {
      padding-bottom: 39px;
      margin-bottom: 30px;
    }

    &__link {
      margin: 0;
      width: 100%;
    }
  }

  .doctor {
    &__link {
      padding: 20px;
      min-height: 365px;
    }

    &__name {
      margin-bottom: 10px;
      width: 100%;
      font-size: 20px;
    }
  }
}

// doctors-page

.doctors-page {
  padding: 80px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }

  & .doctor {
    border: none;
    background-color: var(--background);
    border-top: 4px solid var(--accent);
  }
}

.doctors-page__item {
  width: 24%;
}

@media (max-width: 1550px) {
  .doctors-page__item {
    width: 32%;
  }
}

@media (max-width: 1440px) {
  .doctors-page__item {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .doctors-page {
    padding: 40px 0;

    &__list {
      flex-direction: column;
    }

    .doctors-page__item {
      width: 100%;
    }
  }
}