.reviews {
  padding: 80px 0;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 57px;
  }

  &__slider {
    position: relative;
    margin-bottom: 60px;
    height: 100%;
  }

  &__swiper {
    overflow: hidden;
  }

  &__link {
    margin: 0 auto;
  }

  & .slider-navigation {
    top: 122px;
  }
}

.review {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: auto;
  min-height: 294px;
  color: var(--text-gray);
  border: 1px solid var(--border);

  &__date {
    margin-bottom: 16px;
  }

  &__name {
    margin-bottom: 12px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--black);
  }

  &__text {
    max-height: 91px;
    overflow: hidden;
    font-style: italic;
    transition: max-height 0.4s ease-out;
  }

  &__button {
    margin-top: auto;
    font-size: 14px;
    font-weight: 700;
    color: var(--accent);
  }

  &__button.hidden {
    display: none;
  }
}

.rating {
  margin-bottom: 16px;
  height: 16px;
  display: flex;
  gap: 4px;
}

.rating span {
  position: relative;
  width: 16px;
  height: 16px;
}

.rating span::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  left: 0;
  top: 0;
  background-image: url("./../img/icons/star-empty.svg");
  background-size: 16px 16px;
}

.rating span.active::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  left: 0;
  top: 0;
  background-image: url("./../img/icons/star.svg");
}

@media (max-width: 768px) {
  .reviews {
    padding: 40px 0;

    &__title {
      margin-bottom: 30px;
    }

    &__slider {
      padding-bottom: 39px;
      margin-bottom: 30px;
    }

    &__link {
      margin: 0;
      width: 100%;
    }
  }

  .review {
    padding: 20px;
    min-height: 292px;
    height: 292px;

    &__date {
      margin-bottom: 14px;
    }

    &__text {
      max-height: 134px;
    }

    &__button {
      margin-top: 20px;
    }
  }
}