.articles {
  padding: 120px 0 80px 0;
  background-color: var(--background);

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__slider {
    position: relative;
    margin-bottom: 60px;
    height: 100%;
  }

  &__swiper {
    overflow: hidden;
  }

  &__link {
    margin: 0 auto;
  }
}

.article {
  border: 1px solid var(--border);
  height: auto;

  &:hover {
    background-color: var(--background-small);
  }

  &__img {
    width: 100%;
    height: 223px;
    object-fit: cover;
    object-position: center;
  }

  &__wrapper {
    padding: 24px;
    color: var(--text-gray);
  }

  &__date {
    position: relative;
    margin-bottom: 12px;
    padding-left: 27px;

    &::before {
      content: "";
      position: absolute;
      width: 18px;
      height: 18px;
      left: 0;
      top: 0;
      background: url("./../img/icons/calendar.svg");
    }
  }

  &__title {
    margin-bottom: 12px;
    display: block;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--black);
  }
}

@media (max-width: 768px) {
  .articles {
    padding: 40px 0;

    &__slider {
      margin-bottom: 30px;
      padding-bottom: 36px;
    }

    &__link {
      margin: 0;
      width: 100%;
    }
  }

  .article {
    &__img {
      height: 168px;
    }

    &__title {
      font-size: 18px;
      line-height: 110%;
    }
  }
}

// article-page

.article-page {
  .text-content {
    padding: 80px 0;

    &__wrapper {
      display: flex;
      gap: 5%;
      margin: 0;
      flex-direction: row;
      max-width: 100%;
    }

    &__flex {
      width: 71%;
    }
  }

  .check {
    margin-bottom: 60px;

    &__img {
      width: 80px;
      height: 80px;
      object-fit: contain;
    }
  }

  & .call-doctor {
    margin-top: 40px;
    margin-bottom: 32px;
    padding: 0;

    &__flex {
      flex-direction: column;
      align-items: flex-end;
      gap: 40px;
    }

    &__title {
      width: 50%;
    }
  }
}

.need-help {
  margin-bottom: 60px;
  padding-left: 40px;
  padding-right: 32px;
  display: flex;
  align-items: flex-end;
  background-image: url("./../img/sections/head-doctor/background-form.webp");
  background-size: 100% auto;
  background-position: center;

  &__title {
    margin-top: 40px;
    margin-bottom: 18px;
    text-align: left;
  }

  &__text {
    display: flex;
    flex-direction: column;
  }

  & .list {
    flex-direction: row;
    gap: 40px;

    & li::before {
      background-color: var(--white);
    }
  }

  &__buttons {
    margin-top: 30px;
    margin-bottom: 40px;
    display: flex;
    gap: 12px;
  }

  &__button {
    width: 214px;
  }

  &__phone {
    width: 214px;
    color: var(--white);
    border-color: var(--white);
  }

  &__doctor {
    position: relative;
  }

  &__bage {
    bottom: 40px;
    right: 0;
    width: 429px;
  }
}

.bage__prof {
  color: var(--text-gray);
}

@media (max-width: 1465px) {
  .need-help__doctor {
    display: none;
  }
}

@media (max-width: 1200px) {
  .article-page {
    .text-content {
      & .check {
        flex-direction: column;
        gap: 20px;

        &__doctor {
          width: 100%;
        }
      }
    }
  }
}

@media (max-width: 1023px) {
  .article-page {
    .text-content {
      &__wrapper {
        display: block;
        padding: 0;
      }

      &__flex {
        width: 100%;
      }
    }
  }
}


@media (max-width: 768px) {
  .article-page {
    .check {
      margin-bottom: 60px;

      &__img {
        width: 58px;
        height: 58px;
      }
    }

    .call-doctor__title {
      width: 100%;
    }
  }

  .need-help {
    margin-bottom: 40px;
    padding: 20px;
    display: block;
    background-size: auto 50%;
    background-position: top right;

    &__title {
      margin-top: 0;
      margin-bottom: 30px;
    }

    & .list {
      flex-direction: column;
      gap: 10px;
    }

    &__buttons {
      margin-bottom: 0;
      flex-direction: column;
    }

    &__button {
      width: 100%;
    }

    &__phone {
      width: 100%;
    }
  }
}