// doctor-page
.doctor-page {
  padding: 30px 0 80px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;

  & .breadcrumbs {
    margin-bottom: 0;
  }

  &__top {
    padding: 41px 41px 34px 41px;
    display: flex;
    gap: 30px;
    width: 100%;
    border: 1px solid var(--border);
    background: var(--background) url("./../img/sections/intro/background-small.webp") no-repeat;
    background-size: auto 100%;
    background-position: center;
    box-shadow: 0px 0px 21.4px 0px #0000000f;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    text-align: left;
  }

  &__img {
    margin-bottom: 20px;
    object-fit: cover;
  }

  &__mobile {
    display: none;
  }

  & .rating span {
    width: 24px;
    height: 24px;
  }

  & .rating span::before {
    width: 24px;
    height: 24px;
    background-size: 24px 24px;
  }

  &__prof {
    margin-bottom: 20px;
    font-size: 18px;
  }

  &__plashka {
    margin-right: auto;
    margin-bottom: 20px;
    padding: 16px 14px;
    border: 1px solid var(--border);

    & span {
      margin-left: 0;
      color: var(--accent);
    }
  }

  &__buttons {
    margin-top: auto;
    display: flex;
    justify-content: center;
    gap: 10px;

    &-inner {
      margin: 0 auto;
      display: flex;
      gap: 10px;
    }
  }

  &__cost {
    padding: 0 20px;
    align-self: center;
    font-size: 14px;
    border-right: 1px solid var(--text-gray);

    &:last-child {
      border: none;
    }

    & span {
      padding: 0 8px;
      font-family: "PTSerif", serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      color: var(--accent);
    }
  }

  &__flex {
    padding: 41px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: calc(50% - 20px);
    border: 1px solid var(--border);
    background-color: var(--background);

    & ul {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
    }

    & ul li {
      padding-left: 17px;
      position: relative;
    }

    & ul li::before {
      content: "";
      width: 6px;
      height: 6px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: var(--accent);
      border-radius: 50%;
    }
  }

  &__flex-title {
    padding-bottom: 18px;
    width: 100%;
    border-bottom: 1px solid var(--border);
    text-align: left;
  }

  &__flex-subtitle {
    margin-bottom: 8px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
  }

  &__spec {
    display: block;
    color: var(--text-gray);
  }

  & .documents {
    padding: 0;

    &__swiper {
      position: relative;
      padding: 20px 20px 36px 20px;
      border: 1px solid var(--border);
    }

    &__slider {
      padding: 0;
      width: 100%;
      height: 230px;
      overflow: hidden;
      border: none;
    }
  }

  & .slider-navigation {
    top: auto;
    left: 20px;
    bottom: 20px;
    transform: none;
    width: calc(98% - 20px);
  }

  &__work {
    padding-left: 18px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    font-size: 18px;
    border-left: 2px solid var(--accent);
  }

  &__work-date,
  &__work-spec {
    font-size: 16px;
    color: var(--text-gray);
  }

  &__services,
  &__days {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  &__service,
  &__day {
    display: flex;
    align-items: center;
    font-size: 18px;
    border: 1px solid var(--border);
    color: var(--text-gray);
  }

  &__service {
    padding: 20px;

    & span {
      margin-left: auto;
      font-family: "PTSerif", serif;
      font-size: 18px;
      font-weight: 700;
      color: var(--accent);
    }
  }

  &__day {
    padding: 13px;

    & .doctor-page__name {
      width: 120px;
    }
  }

  &__name {
    font-size: 18px;
    font-weight: 700;
    color: var(--black);
  }

  &__slot {
    position: relative;
    padding-left: 16px;
    font-size: 14px;

    &::before {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      left: 0;
      top: 4px;
      background: url("./../img/icons/clock-black.svg");
      background-size: 12px 12px;
    }
  }

  &__slots {
    margin-left: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    & span {
      padding: 5px 13px;
      background-color: #f3f4f6;
      border: 1px solid var(--border);
    }
  }

  &__note {
    padding: 13px;
    font-size: 14px;
    color: var(--text-gray);
    background-color: #f3f4f6;
    border: 1px solid var(--border);
  }
}

.rating a {
  margin-left: 16px;
  font-weight: 700;
  color: var(--accent);
}

@media (max-width: 1640px) {
  .doctor-page .documents {

    &__swiper {
      padding: 10px;
    }

    &__slider {
      height: auto;
      padding-bottom: 40px;
    }

    & .swiper-scrollbar {
      margin: 0;
      width: 98%;
    }

    & .document__img {
      width: 232px;
      height: 337px;
      margin: 0 auto;
    }
  }
}

@media (max-width: 1023px) {
  .doctor-page__buttons {
    flex-direction: column-reverse;
  }
}

@media (min-width: 769px) AND (max-width: 1320px) {

  .doctor-page__service,
  .doctor-page__day {
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
}

@media (max-width: 768px) {
  .doctor-page {
    padding: 6px 0 40px 0;
    flex-direction: column;
    border-top: 1px solid var(--border);
    gap: 27px;

    & .breadcrumbs {
      margin-bottom: 0;
    }

    &__top {
      padding: 20px;
      display: flex;
      flex-direction: column;
      background-image: none;
      gap: 11px;
    }

    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__img {
      width: 100%;
      max-height: 268px;
    }

    &__mobile {
      display: block;
    }

    &__desktop {
      display: none;
    }

    & .rating {
      gap: 0;
    }

    & .rating span {
      width: 28px;
      height: 28px;
    }

    & .rating span::before {
      width: 28px;
      height: 28px;
      background-size: 28px 28px;
    }

    &__title {
      margin-bottom: 10px;
    }

    &__prof {
      margin-bottom: 23px;
      font-size: 18px;
    }

    &__plashka {
      margin-right: 0;
      padding: 14px 16px;
      width: 100%;

      & span {
        display: block;
        margin-left: 0;
      }
    }

    &__buttons {
      margin-top: 0;
      width: 100%;

      &-inner {
        flex-wrap: wrap;
        width: 100%;
      }
    }

    &__button {
      width: 100%;
    }

    &__cost {
      margin-bottom: 25px;
      padding: 0;
      width: 48%;
      border: none;

      & span {
        margin-top: 4px;
        padding: 0;
        display: block;
        font-size: 18px;
        line-height: 110%;
      }
    }

    &__flex {
      padding: 20px;
      padding-right: 18px;
      width: 100%;

      & ul {
        gap: 3px;
      }
    }

    &__flex-title {
      padding-bottom: 18px;
    }

    &__flex-subtitle {
      margin-bottom: 11px;
    }

    &__work {
      font-size: 16px;
      gap: 2px;
    }

    &__work-date,
    &__work-spec {
      font-size: 14px;
    }

    &__service,
    &__day {
      font-size: 16px;
    }

    &__service {
      position: relative;
      padding: 12px;
      gap: 10px;

      & span {
        position: absolute;
        margin-left: 0;
        top: 14px;
        right: 14px;
        font-size: 16px;
        line-height: 110%;
      }

      & .doctor-page__name {
        margin-bottom: 10px;
        width: 65%;
      }
    }

    &__day {
      padding: 11px;
      padding-bottom: 13px;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 20px;

      & .doctor-page__name {
        width: auto;
      }
    }

    &__name {
      font-size: 16px;
    }

    &__slots {
      margin-left: 0;
      width: 100%;
      flex-wrap: wrap;
      gap: 7px;

      & span {
        padding: 4px 16px;
      }
    }

    &__note {
      padding: 11px;
      line-height: 144%;
    }
  }
}