.management {
  padding: 80px 0 30px 0;
  background-color: var(--background);

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 5px;
  }

  &__slider {
    position: relative;
    margin-bottom: 50px;
    height: 100%;
  }

  &__swiper {
    overflow: hidden;
  }

  &__link {
    margin: 0 auto;
  }
}

.manager {
  padding: 25px;
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 340px;
  color: var(--text-gray);
  border: 1px solid var(--border);

  &__wrapper {
    margin-bottom: 26px;
    padding-bottom: 26px;
    border-bottom: 1px solid var(--border);
  }

  &__img {
    float: right;
    border-radius: 50%;
  }

  &__name {
    margin-bottom: 8px;
    width: 76%;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 115%;
    color: var(--black);
  }

  &__button {
    margin-top: auto;
    width: 100%;
    font-weight: 700;
  }

  &__phone {
    margin-bottom: 12px;

    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 3px;
      background: url("./../img/icons/phone.svg");
    }
  }

  &__email {
    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 3px;
      background: url("./../img/icons/email.svg");
    }
  }
}

.manager__phone, .manager__email {
  position: relative;
  padding-left: 28px;
}

@media (max-width: 768px) {
  .management {
    padding: 40px 0 7px 0;

    & .subtitle {
      margin-bottom: 29px;
    }

    &__slider {
      padding-bottom: 39px;
      margin-bottom: 30px;
    }
  }

  .manager {
    padding: 21px;
    min-height: 284px;

    &__wrapper {
      margin-bottom: 20px;
      padding-bottom: 20px;
    }

    &__img {
      width: 48px;
      height: 48px;
    }

    &__name {
      margin-bottom: 6px;
      width: 100%;
      font-size: 20px;
      line-height: 110%;
    }
  }
}