// gallery

.gallery {
  padding: 80px 0 120px 0;

  &__title {
    margin-bottom: 60px;
  }

  &__slider {
    position: relative;
    margin-bottom: 18px;
  }

  &__swiper {
    overflow: hidden;
  }

  &__slide {
    position: relative;
    overflow: hidden;
    height: 489px;
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: 1;
  }

  &__bage {
    position: absolute;
    bottom: 32px;
    left: 32px;
    padding: 24px;
    width: 448px;
    background-color: var(--background);
    box-shadow: 0px 4px 6px -4px #0000001A, 0px 10px 15px -3px #0000001a;
    z-index: 2;

    p {
      font-size: 14px;
    }
  }

  &__bage-title {
    margin-bottom: 12px;
    display: block;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--accent);
  }

  &__wrapper {
    padding: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 1px solid var(--border);
  }

  &__wrapper-title {
    margin-bottom: 32px;
    width: 100%;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
  }

  &__list {
    gap: 12px;
  }
}

@media (max-width: 768px) {
  .gallery {
    padding: 40px 0;

    &__title {
      margin-bottom: 30px;
    }

    &__slider {
      margin-bottom: 20px;
    }

    &__slide {
      position: relative;
      overflow: hidden;
      height: 404px;
    }

    &__bage {
      bottom: 12px;
      left: 12px;
      padding: 14px;
      width: 92%;

      & p {
        line-height: 140%;
      }
    }

    &__bage-title {
      margin-bottom: 12px;
      font-size: 18px;
      line-height: 110%;
    }

    &__wrapper {
      padding: 20px;
      flex-direction: column;
      gap: 8px;
    }

    &__wrapper-title {
      margin-bottom: 13px;
      font-size: 20px;
      line-height: 110%;
    }

    &__list {
      gap: 7px;
    }
  }
}

// gallery-page

.gallery-page {
  padding: 80px 0;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}

.gallery-page__item {
  width: 32%;

  & img {
    width: 100%;
    height: 440px;
    object-fit: cover;
    object-position: center;
    overflow: hidden;
  }

  & img:nth-child(6n+1) {
    width: calc(64% + 20px);
    height: 440px;
    object-fit: cover;
    overflow: hidden;
  }
}

@media (max-width: 1440px) {
  .gallery-page__item {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .gallery-page {
    padding: 40px 0;

    &__list {
      flex-direction: column;
    }

    .gallery-page__item {
      width: 100%;

      & img {
        height: 228px;
      }

      & img:nth-child(6n+1) {
        height: 228px;
      }
    }
  }
}