// infusions

.infusions {
  padding: 120px 0;

  &__slider {
    overflow: hidden;
  }
}

@media (min-width: 769px) {
  .infusions .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.infusion {
  padding: 25px;
  width: 24%;
  min-height: 404px;
  height: auto;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border);

  &__icon {
    margin-bottom: 23px;
  }

  &__name {
    margin-bottom: 21px;
    font-family: "PTSerif", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  &__description {
    margin-bottom: 21px;
    color: var(--text-gray);
  }

  &__description-title {
    margin-bottom: 12px;
    font-weight: 700;
  }
}

@media (max-width: 1300px) {
  .swiper-wrapper {
    gap: 20px 0;
  }
  .infusion {
    width: 49%;

    &__name {
      font-size: 29px;
    }
  }
}

@media (max-width: 768px) {
  .infusions {
    padding: 40px 0 37px 0;

  & .subtitle {
    margin-bottom: 30px;
  }

  &__slider {
    position: relative;
    min-height: 425px;
    height: 100%;
  }
}

  .infusion {
    width: 100%;
    min-height: 353px;
    height: auto;

    &__icon {
      margin-bottom: 19px;
    }

    &__name {
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 120%;
    }

    &__description-title {
      margin-bottom: 9px;
    }
    
    &__list {
      gap: 4px;
    }

    &__item {
      padding-left: 25px;
    }

    &__item::before {
      width: 18px;
      height: 18px;
    }
  }

}