// prices

.prices {
  padding: 120px 0 84px 0;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__table {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 18px;
    box-shadow: 0px 0px 41.1px 0px #0000001a;
  }

  &__head {
    display: block;
    padding: 18px 32px;
    width: 100%;
    font-family: "PTSerif", serif;
    color: var(--white);
    background-color: var(--accent);
  }

  &__body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
    border: 1px solid var(--border);
  }

  &__table tr {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    align-items: center;
    gap: 10px;
  }

  &__body tr {
    padding: 20px 32px 21px 32px;
    border-bottom: 1px solid var(--border);
    line-height: 127%;
  }

  &__table tr:last-child {
    border-bottom: none;
  }

  &__body tr:nth-child(2n) {
    background-color: #f9fafb80;
  }

  &__body tr:hover {
    background-color: var(--background-small);
  }

  &__head tr th {
    display: block;
  }

  &__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    gap: 10px;
  }

  &__cost {
    color: var(--accent);
    font-size: 16px;
    white-space: nowrap;
  }

  &__button {
    margin: 0 auto;
    text-decoration: underline;
  }
}

@media (max-width: 768px) {
  .prices {
    padding: 40px 0;

    &__table {
      font-size: 16px;
      line-height: 121%;
    }

    &__body {
      border: none;
    }

    &__head {
      padding: 18px 10px;
    }

    &__body tr {
      padding: 20px 10px 21px 10px;
    }
  }
}